import h from 'snabbdom/h';
import { filter, map } from 'ramda';
import { glowFilter, orbit, home, pan, zoom, fullscreen, exitfullscreen,
  vrMode, lenticularMode, deviceTracking, nodeRotate, nodeMove, nodeScale
} from './icons';
import { manipulators, useManipulator, getInitialCameraPosition, useCamera, getActiveColor,
   toggleFullscreen, isFullscreen, getHoverColor, isVRMode, isLenticularMode,
   toggleVRMode, toggleLenticularMode, isDeviceTracking, toggleDeviceTracking, displayTool,
} from '../modules/player';
import timeline from './timeline';

const manipulatorInfos = [
  {key: 'orbit', svg: orbit, label: 'Orbit'},
  {key: 'pan', svg: pan, label: 'Pan'},
  {key: 'zoom', svg: zoom, label: 'Zoom'},
  {key: 'home', svg: home, label: 'Home'},
  {key: 'fullscreen', svg: fullscreen, isActive: isFullscreen, label: 'Full screen', activeLabel: 'Exit Full screen'},
  {key: 'vrMode', svg: vrMode, isActive: isVRMode, label: 'VR mode', activeLabel: 'Exit VR Mode'},
  {key: 'lenticularMode', svg: lenticularMode, isActive: isLenticularMode, label: 'Lenticular mode', activeLabel: 'Exit Lenticular Mode'},
  {key: 'deviceTracking', svg: deviceTracking, isActive: isDeviceTracking, label: 'Head Tracking', activeLabel: 'Disable Head Tracking'},
  {key: 'nodeRotate', svg: nodeRotate, label: 'Rotate Node'},
  {key: 'nodeMove', svg: nodeMove, label: 'Move Node'},
  {key: 'nodeScale', svg: nodeScale, label: 'Scale Node'},
]

const isTouch = __BROWSER__ && (!!("ontouchstart" in window) || window.navigator.msMaxTouchPoints > 0);

export default function manipulatorTools(store) {
  const manipulator = store.getIn(['player','activeManipulator']);

  const liStyle = {
    cursor: 'pointer',
    float: 'left',
    borderRadius: '5px',
    padding: '5px',
    boxSizing: 'border-box',
  };

  function handleResetCamera() {
    const navigator = store.getNavigator();
    if (navigator) navigator.reset();
  };

  function click(what) {
    return (ev) => {
      if (what === 'home') handleResetCamera();
      else if (what === 'fullscreen') store.dispatch(toggleFullscreen());
      else if (what === 'vrMode') store.dispatch(toggleVRMode());
      else if (what === 'lenticularMode') store.dispatch(toggleLenticularMode());
      else if (what === 'deviceTracking') store.dispatch(toggleDeviceTracking());
      else store.dispatch(useManipulator(manipulators[what]));
    }
  }

  const ulStyle = {position: 'relative', bottom: 0, left: 0, listStyleType: 'none', padding: '0 5px', margin: 0, zIndex: 2147483647};
  const activeColor = getActiveColor(store);
  const hoverColor = getHoverColor(store);

  const hoverStyle = isTouch ? '' : `
div.claraplayer ul.tools li:not(.active):hover svg path {
  fill: ${hoverColor};
}
`;
  const spanStyle = {
    position: 'absolute',
    left: '5px',
    right: '5px',
    bottom: '5px',
    display: 'flex',
    flexWrap: 'nowrap',
    height: '54px',
  };

  let toolChildren = [
    h('style', { props: { innerHTML: hoverStyle } }),
    h('ul.tools', {style: ulStyle}, [glowFilter()].concat(map((info) => {
      const active = info.isActive ? info.isActive(store) : (manipulators[info.key] === manipulator);
      const label = active && info.activeLabel ? info.activeLabel : info.label;
      return h('li' + (active ? '.active' : ''), {on: {click: click(info.key)}, style: liStyle}, [info.svg({width: 34, height: 34, title: label}, { active, activeColor })]);
    }, filter((info) => displayTool(store, info.key), manipulatorInfos)))),
  ];

  const tLine = timeline(store);
  if (tLine) toolChildren.push(tLine);

  return h('span', {style: spanStyle}, toolChildren);
};



