import skin from '../../polyMesh/geometric/operator/skin';

const Skin = {
  schema: {
    boneNodeList: { type: 'NodeList', label: 'Select Bones', plug: 'Bone' },
    optionalRootBone: { type: 'Node', label: 'Optional Root Bone', defaultValue: null, filter: function(n) { return n.isBone(); } },
  },

  update: function(operator, primitive, attrs) {
    const skinNode = attrs.node;
    const skinWorldTransform = operator.getWorldTransform(skinNode.get('id'));

    // Ensure we reference the bone node list, so that the dependency
    // graph gets updated properly.
    const nodeList = operator.get('boneNodeList');
    const rootBone = operator.get('optionalRootBone');
    let rootBoneIndex = -1;

    //get world transforms for each bone
    const boneWorldTransforms = nodeList.map((node) => {
      return operator.getWorldTransform(node.get('id'));
    }).toArray();

    //get the index of the root bone
    if( rootBone ) {
      for( let i = 0, il = nodeList.size; i < il; i ++ ) {
        let boneNode = nodeList.get( i );
        if (boneNode == rootBone ) {
          rootBoneIndex = i;
          break;
        }
      }
    }

    const polyMesh = primitive.get('mesh');
    const skinToWorldTransform = skinWorldTransform;

    const newPolymesh = skin(polyMesh, skinToWorldTransform, boneWorldTransforms, rootBoneIndex);

    return primitive.set('mesh', newPolymesh);
  }
}

export default Skin;



