const apiRoot = __URLROOT__+'/api';

export function reportError(msg) {
  return {type: SCENE_ERROR, payload: msg};
}

/**
 * sets the active scene.
 *
 * @param {string} id the scene uuid
 */
export function useScene(id) {
  return {type: USE_SCENE, payload: id};
}


/**
 * Returns the active scene Id
 *
 * @public
 * @returns {uuid}
 *
 */
export function getSceneId(store) {
  return store.getIn(['sceneIO', 'id']);
};

/**
 * Loads the published scene
 *
 * @public
 * @param {string} id the scene uuid.
 * @param {string} transitString the published, exported scene
 * @returns {Promise}
 *
 */
export function loadScene(id, transitString) {
  return (store) => {
    var payload = store.read(transitString);
    return store.dispatch({type: LOAD_SCENE, payload});
    //try {
    //} catch (e) {
    //  console.log('Error reading transit format', e);
    //  return store.dispatch(errorLoadingScene(id));
    //}
  };
}

export function errorLoadingScene(id) {
  return reportError('Error Loading Scene');
}

export function markRenderingScene() {
  return { type: RENDERING_SCENE };
}

export function markRenderedScene() {
  return { type: RENDERED_SCENE }
}

export function getOrFetchScene(id) {
  return (store) => {
    return store.callApi({
      types: [LOADING_SCENEGRAPH,LOADED_SCENEGRAPH,ERROR_LOADING_SCENEGRAPH],
      url: `${apiRoot}/scenes/${id}`,
      payload: id
    });
  }
};


/**
 * Fetch the scene
 *
 * If `hash` is provided it will fetch directly from resources, otherwise it will fetch from
 * `/api/scenes/:uuid/published`.
 *
 *  @public
 *  @param {uuid} sceneId
 *  @param {String} hash - md5 hash of published content (Optional)
 *
 */
export function fetch(sceneId, hash) {
  return (store) => {
    if (store.getIn(['sceneGraph', sceneId])) return Promise.resolve(true);

    var fetch = hash ?
      fetchText(hash)(store) :
      store.callApi({
        contentType: 'text',
        types: [LOADING_SCENEGRAPH,LOADED_SCENEGRAPH,ERROR_LOADING_SCENEGRAPH],
        url: `${apiRoot}/scenes/${sceneId}/published`,
        payload: sceneId
      });

    const promise = fetch.then((transit) => {
      return store.dispatch(loadScene(sceneId, transit));
    });

    if (hash && __BROWSER__ && __GOOGLE_ANALYTICS_TOKEN__) {
      store.callApi({
        contentType: 'text',
        method: 'POST',
        url: `https://www.google-analytics.com/collect?v=1&tid=${__GOOGLE_ANALYTICS_TOKEN__}&cid=555&t=pageview&dh=${window.location.hostname}&dp=/player/v2/${sceneId}?static=${hash}`,
      });
    }

    return promise;
  };
};

/**
 * Fetch, Load and Use published scene
 *
 * If `hash` is provided it will fetch directly from resources, otherwise it will fetch from
 * `/api/scenes/:uuid/published`.
 *
 *  @public
 *  @param {uuid} sceneId
 *  @param {String} hash - md5 hash of published content (Optional)
 *
 */
export function fetchAndUse(sceneId, hash) {
  return (store) => {
    store.dispatch(useScene(sceneId));
    return store.dispatch(fetch(sceneId, hash));
  };
};

export function loadingProgress({getIn, dispatch}) {
  return getIn(['sceneIO','progress']);
};

/*
 * Have preloaded commands run, and are all assets loaded
 */
export function areAssetsLoaded(store) {
  return store.getIn(['sceneIO', 'state']) >= states.PRELOADED && store.getIn(['sceneIO', 'loadingFiles']).size === 0;
}


/*
 * Are all assets loaded before preload commands are run
 */
export function areAssetsPreloaded(store) {
  return store.getIn(['sceneIO', 'state']) >= states.RENDERING && store.getIn(['sceneIO', 'loadingFiles']).size === 0;
}

export function isSceneLoaded(store) {
  return store.getIn(['sceneIO','loaded']);
}
export function isSceneRendered(store) {
  return store.getIn(['sceneIO','state']) >= states.RENDERED;
}

export function markPreloaded() {
  return (store) => {
    const preloadCommands = store.getIn(['sceneIO', 'preloadCommands']);

    Promise.all(preloadCommands.map((fn) => {
      return Promise.resolve(fn());
    }).toArray()).then(() => {
      store.dispatch({ type: PRELOADED_SCENE });
    });
  }
}

export function preloadCommand(fn) {
  return { type: PRELOAD_COMMAND, payload: fn };
}

export function markLoaded() {
  return { type: LOADED_SCENE };
}

export function markLoadingFile(id) {
  return { type: LOADING_FILE, payload: id };
};

export function markLoadedFile(id) {
  return { type: LOADED_FILE, payload: id };
};

function convertPlugs(plugArray) {
  var plugs = {};
  plugArray.forEach((plug) => {
    plugs[plug.type] = plug.operators.map((op) => {
      let name = op.name;
      let type = op.type || name;
      return [type, omit(['_id','primitive','name','type'], op), name];
    });
  })
  return plugs;
};

const convertNode = curry(function(store, sceneJSON, parent, json) {
  const id = json._id;
  const name = json.name;
  const type = json.type;

  const plugs = convertPlugs(json.plugs);

  store.dispatch(addNode({id, name, type, plugs, parent}));

  forEach(convertNode(store, sceneJSON, id), json.nodes);
});

function convertFiles(store, files) {
  forEach((file) => {
    store.dispatch(addFile({id: file._id, ...omit(['_id', '__v'], file)}));
  }, files);
}

export function convertFromJSON(store, json) {
  convertNode(store, json, null, json);
  convertFiles(store, json.files);

  mapObjIndexed((clip, id) => {
    store.dispatch(addClip(clip, id));
  }, json.clips);
};

// Load scene from old json format, do a client side conversion, and use
// the new converted scene.
export function fetchConvertAndUse(sceneId) {
  return (store) => {
    if (!sceneId || sceneId.length !== 36) return store.dispatch(reportError('Unknown sceneId'));
    return getOrFetchScene(sceneId)(store).then((result) => {
      try {
        convertFromJSON(store, result);
        return store.dispatch(useScene(sceneId));
      } catch (e) {
        return store.dispatch(reportError(e.message));
        console.log(e.stack);
      }
    }).catch((err) => {
      return store.dispatch(reportError(err.responseText));
    });
  }
}

export function writeScene(store) {
  return store.write({
    scene: store.get('sceneGraph'),
    assets: getActiveAssets(store),
    clips: store.getIn(['animation','clips']),
  });
}

export function exportJSON(store) {
  return {
    scene: store.get('sceneGraph').toJS(),
    assets: getActiveAssets(store).toJS()
  };
}

export function numErrors(store) {
  return store.getIn(['sceneIO','errors']).size;
}

export function getErrors(store) {
  return store.getIn(['sceneIO','errors']);
}

const publicApi = {
  reducer,
  actions: { fetch, fetchAndUse, loadScene, useScene, reportError, preloadCommand },
  selectors: { isSceneLoaded, isSceneRendered, loadingProgress, getErrors, numErrors, getSceneId }
};
export default publicApi;





