
import { forEach } from 'ramda';
import rsvp from 'rsvp';

// Simple, persistent api cache for now.
const apiCache = {};
const fetchingCache = {};

export default function apiBase(fetchNext) {
  return (store, {types, url, fireAndForget, payload, queryKey, requestPayload, cache, method, contentType, body, files, success, failure}) => {
    let urls = Array.isArray(url) ? url : [url];
    if (!contentType) contentType = 'json';
    const [requestType, successType, failureType] = types || ['IGN','IGN','IGN'];

    // If we provide a 'queryKey', there can be only one query at once
    if (queryKey) {
      if (apiCache[queryKey]) return fireAndForget ? apiCache[queryKey] : Promise.resolve(apiCache[queryKey]);
      if (fetchingCache[queryKey]) {
        fetchingCache[queryKey].push({ payload, store });
        store.dispatch({type: requestType, payload});
        return;
      }
      fetchingCache[queryKey] = [{ payload, store }];
    }

    store.dispatch({type: requestType, payload});

    var promise = new rsvp.Promise((resolve, reject) => {
      function onFailure(error) {
        if (queryKey) {
          forEach(({ payload, store }) => {
            store.dispatch({type: failureType, payload, error});
          }, fetchingCache[queryKey]);
          delete fetchingCache[queryKey];
        } else {
          store.dispatch({type: failureType, payload, error});
        }
        if (failure) failure(error);
        reject(error);
      };

      function onSuccess(response) {
        if (queryKey) {
          apiCache[queryKey] = response;
          forEach(({ payload, store }) => {
            store.dispatch({type: successType, payload, response});
          }, fetchingCache[queryKey]);
          delete fetchingCache[queryKey];
        } else {
          store.dispatch({type: successType, payload, response});
        }
        if (success) success(response);
        resolve(response);
      };


      fetchNext(urls[0], urls.slice(1), onSuccess, onFailure, { contentType, method, body, files });
    });

    return fireAndForget ? null : promise;
  }
}




