import { List } from 'immutable';
import THREE from 'three';
import subdivision2 from '../../polyMesh/geometric/operator/subdivision2';

const MeshSmooth = {
  schema: {
    subdivisions: { label: 'Subdivisions', type: 'Number', defaultValue: 1, minValue: 0, maxValue: 4 },
    hardEdges: { label: 'Hard Edges', type: 'Boolean', defaultValue: false }
  },

  update: function(operator, primitive) {
    const mesh = primitive.get('mesh');
    return mesh ? primitive.set('mesh', subdivision2(mesh)) : primitive;
  }
}

export default MeshSmooth;



