import THREE from 'three';
import {Map,List} from 'immutable';
import createReducer from './utils/createReducer';
import { get, getWorldTransform, set } from './scene';
import { queueAnimation } from './animation';
import { virtualCameraId, vrCameraId } from './sceneGraph';
import updateCameraLookAtTarget from '../translators/utils/updateCameraLookAtTarget';
import WEBVR from 'webvr';
/**
 * This module supports the UI for the player.
 *
 * @module player
 * @public
 */

export const manipulators = {
  orbit: 'orbit',
  pan: 'pan',
  zoom: 'zoom',
  nodeRotate: 'nodeRotate',
  nodeMove:'nodeMove',
  nodeScale:'nodeScale'
};


const USE_MANIPULATOR='USE_MANIPULATOR';
const ADD_TOOL='ADD_TOOL';
const REMOVE_TOOL='REMOVE_TOOL';
const SHOW_TOOL='SHOW_TOOL';
const HIDE_TOOL='HIDE_TOOL';
const SET_CAMERA_ATTRS='SET_CAMERA_ATTRS';
const SET_CAMERA_POSITION='SET_CAMERA_POSITION';
const SET_RECT='SET_RECT';
const DISPLAY_THUMBNAIL='DISPLAY_THUMBNAIL';
const SET_ACTIVE_COLOR='SET_ACTIVE_COLOR';
const SET_HOVER_COLOR='SET_HOVER_COLOR';
const SET_PLAYER_ELEMENT='SET_PLAYER_ELEMENT';
const REQUEST_FULL_SCREEN='REQUEST_FULL_SCREEN';
const EXIT_FULL_SCREEN='EXIT_FULL_SCREEN';
const SET_FULL_SCREEN='SET_FULL_SCREEN';
const REQUEST_NORMAL_MODE='REQUEST_NORMAL_MODE';
const REQUEST_VR_MODE='REQUEST_VR_MODE';
const REQUEST_LENTICULAR_MODE='REQUEST_LENTICULAR_MODE';
const ENABLE_DEVICE_TRACKING='ENABLE_DEVICE_TRACKING';
const DISABLE_DEVICE_TRACKING='DISABLE_DEVICE_TRACKING';
const SET_BACKGROUND_COLOR='SET_BACKGROUND_COLOR';
const ENABLE_MOUSE_CONTROLS='ENABLE_MOUSE_CONTROLS';
const DISABLE_MOUSE_CONTROLS='DISABLE_MOUSE_CONTROLS';
const REQUEST_STEREO_MODE='REQUEST_STEREO_MODE';
const REQUEST_PARALLAX_MODE='REQUEST_PARALLAX_MODE';
const EXIT_STEREOPARALLAX_MODE='EXIT_STEREOPARALLAX_MODE';
const SET_CONSTRAIN_CAMERA_RADIUS='SET_CONSTRAIN_CAMERA_RADIUS';
const SET_MANIPULATION_NODE='SET_MANIPULATION_NODE';

const initialState = Map({
  activeColor: '#ffc85f',
  hoverColor: '#C08618',
  fullscreenBackgroundColor: '#FFF',
  activeManipulator: manipulators.orbit,
  tools: List(),
  displayTools: Map({
    orbit: true, pan: true, zoom: true, home: true, fullscreen: true,
    vrMode: false, lenticularMode: false, deviceTracking: false,
    nodeRotate:false, nodeMove:false, nodeScale:false
  }),
  enableTools: Map({
    orbit: true, pan: true, zoom: true, home: true, fullscreen: true,
    vrMode: true, lenticularMode: true, deviceTracking: true,
    nodeRotate: true, nodeMove: true, nodeScale: true
  }),
  fullScreen: false,
  renderingMode: 'normal',
  deviceTracking: false,
  camera: Map({
    polarAngle: null, azimuthAngle: null, boundingRadius: null
  }),
  rect: Map({width: 400, height: 300}),
  initialCameraWorldMatrix: null,
  initialTargetWorldMatrix: null,
  cameraWorldMatrix: null,
  displayThumbnail: true,
  playerElement: null,
  canvasElement: null,
  prevCamera: null,
  orientationHandler: null,
  constrainCameraRadius: null,
  manipulationId: null,
});

function doRequestVRmode(el) {
  window.navigator.getVRDisplays().then(function(displays) {
    if (displays.length > 0) {
      displays[0].requestPresent([{source: el}]);
    }
  });

}

function doExitVRmode() {
  window.navigator.getVRDisplays().then(function(displays) {
    if (displays.length > 0) {
      displays[0].exitPresent();
    }
  });
}

const reducer = createReducer(initialState, {
  [USE_MANIPULATOR](state, manipulator) {
    return state.set('activeManipulator', manipulator);
  },
  [ADD_TOOL](state, {name, tool}) {
    if (typeof state.getIn(['displayTools', name]) === 'undefined'){
      state = state.setIn(['displayTools', name], true);
    }
    return state.updateIn(['tools'], List(), (list) => list.push(Map({name, tool})));
  },
  [REMOVE_TOOL](state, name) {
    return state.setIn(['displayTools', name], false).updateIn(['tools'], List(), (list) => {
      const idx = list.findIndex((m) => m.get('name') === name);
      return idx===-1 ? list : list.delete(idx);
    });
  },
  [SHOW_TOOL](state, name) {
    return state.setIn(['displayTools', name], true);
  },
  [HIDE_TOOL](state, name) {
    return state.setIn(['displayTools', name], false);
  },
  [SET_CAMERA_ATTRS](state, attrs) {
    return state.mergeIn(['camera'], attrs);
  },
  [SET_RECT](state, attrs) {
    return state.mergeIn(['rect'], attrs);
  },
  [SET_CAMERA_POSITION](state, attrs) {
    return state.merge(attrs);
  },
  [SET_PLAYER_ELEMENT](state,  { playerEl, canvasEl }) {
    return state.set('playerElement', playerEl).set('canvasElement', canvasEl);
  },
  [DISPLAY_THUMBNAIL](state, display) {
    return state.set('displayThumbnail', !!display);
  },
  [SET_ACTIVE_COLOR](state, color) {
    return state.set('activeColor', color);
  },
  [SET_HOVER_COLOR](state, color) {
    return state.set('hoverColor', color);
  },
  [SET_BACKGROUND_COLOR](state, color) {
    return state.set('fullscreenBackgroundColor', color);
  },
  [REQUEST_FULL_SCREEN](state) {
    return state.set('fullScreen', true);
  },
  [EXIT_FULL_SCREEN](state) {
    return state.set('fullScreen', false);
  },
  [SET_FULL_SCREEN](state, fullScreen) {
    return state.set('fullScreen', fullScreen);
  },
  [REQUEST_LENTICULAR_MODE](state, { trackDevice, cameraId }) {
    return state.merge({
      renderingMode: 'lenticular',
      deviceTracking: trackDevice,
      prevCamera: cameraId
    });
  },
  [REQUEST_VR_MODE](state, { cameraId }) {
    return state.merge({
      renderingMode: 'vr',
      deviceTracking: true,
      prevCamera: cameraId
    });
  },
  [REQUEST_NORMAL_MODE](state) {
    return state.merge({
      renderingMode: 'normal',
      deviceTracking: false,
    });
  },
  [ENABLE_DEVICE_TRACKING](state) {
    return state.set('deviceTracking', true);
  },
  [DISABLE_DEVICE_TRACKING](state) {
    return state.set('deviceTracking', false);
  },
  [ENABLE_MOUSE_CONTROLS](state) {
    return state.mergeIn(['enableTools'], {
      orbit: true,
      pan: true,
      zoom: true
    });
  },
  [DISABLE_MOUSE_CONTROLS](state){
    return state.mergeIn(['enableTools'], {
      orbit: false,
      pan: false,
      zoom: false
    });
  },
  [SET_CONSTRAIN_CAMERA_RADIUS](state, radius){
    return state.set('constrainCameraRadius', radius);
  },
  [SET_MANIPULATION_NODE](state, id){
    return state.set('manipulationId', id);
  },
});

export function useManipulator(manipulator) {
  return {type: USE_MANIPULATOR, payload: manipulator};
}

/**
 * Add a tool (See {@tutorial sdk-Tools})
 *
 * @public
 * @param {Tool} Tool
 * @param {String} name provide a name for the tool to allow removing it.
 * @returns {Promise}
 *
 */
export function addTool(tool, name) {
  return { type: ADD_TOOL, payload: { name, tool } };
}

/**
 * Remove a tool (See {@tutorial sdk-Tools})
 *
 * @public
 * @param {String} name The name of the tool to remove
 * @returns {Promise}
 */
export function removeTool(name) {
  return { type: REMOVE_TOOL, payload: name };
}

export function showTool(name) {
  return {type: SHOW_TOOL, payload: name};
};

export function hideTool(name) {
  return {type: HIDE_TOOL, payload: name};
};

export function getRect(store) {
  return store.getIn(['player', 'rect']).toJS();
};

export function setRect(attrs) {
  return {type: SET_RECT, payload: attrs};
};

export function setCameraAttrs(attrs) {
  return {type: SET_CAMERA_ATTRS, payload: attrs};
};

export function getCameraAttrs(store) {
  return store.getIn(['player','camera']);
};

export function getCameraPosition(store) {
  return store.getIn(['player','cameraWorldMatrix']);
};

export function useCamera(id) {
  return set({ scene: true, plug: 'Player', property: 'camera' }, id);
};

export function getCamera(store) {
  const camera = get(store, { plug: 'Player', property: 'camera' });
  if (camera) return camera.get('id');
  const vrMode = store.getIn(['Player', 'renderingMode']);
  return (vrMode ==='stereo' || vrMode==='parallax') ? vrCameraId : virtualCameraId;
};

export function getTools(store) {
  return store.getIn(['player','tools']);
};

export function setCameraPosition(attrs) {
  return {type: SET_CAMERA_POSITION, payload: attrs};
};

export function displayTool(store, key) {
  let displayMode = true;
  if(store.getIn(['player', 'renderingMode'])==='vr' && key!=='vrMode') displayMode = false;
  return store.getIn(['player', 'displayTools', key])&&store.getIn(['player', 'enableTools', key]) && displayMode;
};

export function getInitialCameraPosition(store) {
  return {
    initialCameraWorldMatrix: store.getIn(['player', 'initialCameraWorldMatrix']),
    initialTargetWorldMatrix: store.getIn(['player', 'initialTargetWorldMatrix'])
  };
};

/**
 *
 * Enable fullscreen mode.
 * @public
 *
 */
export function requestFullscreen() {
  return (store) => {
    const playerEl = store.getIn(['player', 'playerElement']);
    ['requestFullscreen','webkitRequestFullscreen','mozRequestFullScreen','msRequestFullscreen'].forEach(function(fn) {
      if (playerEl[fn]) return playerEl[fn]();
    });
    store.dispatch({ type: REQUEST_FULL_SCREEN });
  }
};

/**
 * Exit fullscreen mode
 * @public
 */
export function exitFullscreen() {
  return (store) => {
    ['exitFullscreen','webkitExitFullscreen','mozCancelFullScreen','msExitFullscreen'].forEach(function(fn) {
      if (document[fn]) return document[fn]();
    });
    return store.dispatch({ type: EXIT_FULL_SCREEN });
  }
};

/**
 * Toggle fullscreen mode
 * @public
 *
 */
export function toggleFullscreen() {
  return (store) => {
    return store.dispatch(isFullscreen(store) ? exitFullscreen() : requestFullscreen());
  }
};

/**
 * Check whether were in fullscreen mode.
 * @public
 *
 */
export function isFullscreen(store) {
  return store.getIn(['player','fullScreen']);
};

/**
 * Set the value of fullscreen (set on an event handler, if the native
 * browser control is used to escape full screen mode.
 */
export function setFullscreen(fullScreen) {
  return (store) => {
    if (isFullscreen(store) !== fullScreen) {
      store.dispatch({ type: SET_FULL_SCREEN, payload: fullScreen });
    }
    if (!fullScreen && isVRMode(store)) {
      store.dispatch(exitVRMode());
    }
  }
};

/**
 * @public
 *
 */
export function isNormalRenderingMode(store) {
  return (store.getIn(['player','renderingMode']) === 'normal');
};

/**
 * Check whether we are in VR mode.
 * @public
 *
 */
export function isVRMode(store) {
  return (store.getIn(['player','renderingMode']) === 'vr');
};

/**
 * Check whether we are in Lenticular rendering mode.
 * @public
 */
export function isLenticularMode(store) {
  return (store.getIn(['player','renderingMode']) === 'lenticular');
};

export function requestVRMode() {
  return (store) => {
    //store.dispatch(requestFullscreen());

    const cameraId = getCamera(store);
    store.getTranslator().enterVR();
    store.dispatch({ type: REQUEST_VR_MODE, payload: { cameraId } });
    store.dispatch(useCamera(vrCameraId));
  }
}

export function requestLenticularMode({ trackDevice }) {
  return (store) => {
    store.dispatch(requestFullscreen());
    const cameraId = getCamera(store);
    store.dispatch({ type: REQUEST_LENTICULAR_MODE, payload: { trackDevice, cameraId} });
  }
};

export function requestNormalMode() {
  return { type: REQUEST_NORMAL_MODE };
};

export function exitVRMode() {
  return (store) => {
    store.getTranslator().exitVR();
    store.dispatch(requestNormalMode());
    const prevCameraId = store.getIn(['player', 'prevCamera']);
    if (prevCameraId) store.dispatch(useCamera(prevCameraId));
  }
}

export function toggleVRMode() {
  return (store) => {
    store.dispatch(isVRMode(store) ? exitVRMode() : requestVRMode());
  }
}

export function toggleLenticularMode() {
  return (store) => {
    store.dispatch(isLenticularMode(store) ?  requestNormalMode() : requestLenticularMode({}) );
  }
};

export function enableDeviceTracking() {
  return { type: ENABLE_DEVICE_TRACKING };
};

export function disableDeviceTracking() {
  return { type: DISABLE_DEVICE_TRACKING };
};

export function toggleDeviceTracking() {
  return (store) => {
    console.log('toggle device tracking', !isDeviceTracking(store));
    store.dispatch(isDeviceTracking(store) ?  disableDeviceTracking() : enableDeviceTracking());
  }
};

export function isDeviceTracking(store) {
  return store.getIn(['player', 'deviceTracking']);
};

export function enableMouseControls(){
  return { type: ENABLE_MOUSE_CONTROLS };
};

export function disableMouseControls(){
  return { type: DISABLE_MOUSE_CONTROLS };
}

export function toggleMouseControls(){
  return (store) => {
    store.dispatch(isMouseControls(store) ? disableMouseControls() : enableMouseControls());
  }
}

export function isMouseControls(store){
  const tools = store.getIn(['player', 'enableTools']);
  return tools.get('orbit') || tools.get('pan') || tools.get('zoom');
}


/**
 * Store the rendered player element
 */
export function setPlayerElement(playerEl, canvasEl) {
  return { type: SET_PLAYER_ELEMENT, payload: { playerEl, canvasEl } };
};

