import { getEvalSceneGraph } from '../modules/utils/evalSceneGraph';
import { Map } from 'immutable';
import { iterateDepthFirstWithFilter } from '../modules/utils/iteration';
import { markPreloaded, markLoaded, isSceneLoaded, areAssetsLoaded, areAssetsPreloaded, markRenderedScene, markRenderingScene, getSceneId } from '../modules/sceneIO';
import { virtualCameraId, vrCameraId } from '../modules/sceneGraph';
import 'webvr';
import { incrementTime } from '../modules/animation';

const defaultNodeFilter = (n) => true;

const defaultFns = {
  onLoad() {},
  postTranslate() {},
  updateView() {},
  handleStateChanges() {},
  dirtyCheck() { return false; }
};

export function createTranslator(store, initTranslator, context) {
  let api = {};
  let translator = initTranslator(store, context, api);
  let nodeFilter = defaultNodeFilter;
  let sceneGraph = Map();
  let evalSceneGraph = Map();
  let nodes = Map();
  let rendered = false;
  let preloaded = false;
  let loaded = false;
  let view = null;
  let player = store.get('player');
  let dirtyCheck = false;

  let updateRequested = false;
  let updating = false;
  let renderingMode = store.getIn(['player','renderingMode']);
  let vrDisplay;

  function translateNodes() {
    const sceneId = getSceneId(store);
    if (!sceneId) return 0;

    const newEvalSceneGraph = getEvalSceneGraph(store);
    const newSceneGraph = store.get('sceneGraph');
    let numUpdated = 0;

    dirtyCheck = translator.dirtyCheck(loaded);

    if (!dirtyCheck && newEvalSceneGraph === evalSceneGraph) return numUpdated;

    function translateNode(id, nextNode, prevNode) {
      let prevResult = evalSceneGraph.getIn(['nodes',id]);
      let nextResult = newEvalSceneGraph.getIn(['nodes',id]);
      if (prevResult !== nextResult || (!nextNode && prevNode) ||
        (dirtyCheck && dirtyCheck(store, id, nextResult, prevResult, nextNode, prevNode))){
        translator.translate(id, nextResult, prevResult, nextNode, prevNode);
        numUpdated++;
      }
    }

    for (let prevNode of iterateDepthFirstWithFilter(sceneGraph, sceneId, nodeFilter)) {
      let id = prevNode.get('id');
      translateNode(id, newSceneGraph.get(id), prevNode);
    }

    for (let nextNode of iterateDepthFirstWithFilter(newSceneGraph, sceneId, nodeFilter)) {
      let id = nextNode.get('id');
      if (sceneGraph.get(id)) continue;
      translateNode(id, nextNode, null);
    }

    translateNode(virtualCameraId, newSceneGraph.get(virtualCameraId), sceneGraph.get(virtualCameraId));
    translateNode(vrCameraId, newSceneGraph.get(vrCameraId), sceneGraph.get(vrCameraId));

    sceneGraph = newSceneGraph;
    evalSceneGraph = newEvalSceneGraph;

    return numUpdated;

    //console.log('translated in: ', (new Date() - s)+'ms', numUpdated+' nodes');
    //return numUpdated;
  }

  function doUpdate() {
  }

  return Object.assign({}, defaultFns, translator, {
    update: function(options={}) {
      // console.log('translator do update');
      const numUpdated = translateNodes();
      //console.log('numUpdated: ', numUpdated);
      let renderedAndLoaded = false;


      if (!rendered && numUpdated) {
        rendered = true;
        store.dispatch(markRenderingScene());
      }

      if (!loaded && preloaded && areAssetsLoaded(store)) {
        loaded = true;
        translator.onLoad();
        store.dispatch(markLoaded());
        renderedAndLoaded = true;
      }

      if (!preloaded && areAssetsPreloaded(store)) {
        preloaded = true;
        store.dispatch(markPreloaded());
      }

      if (loaded && store.get('player') !== player) {
        player = store.get('player');
        translator.updateView(player);
      }

      if (store.getIn(['player','renderingMode']) !== renderingMode) {
        renderingMode = store.getIn(['player','renderingMode']);
      }

      translator.postTranslate(renderingMode, loaded);

      if (renderedAndLoaded) {
        store.dispatch(markRenderedScene());
      }

      store.dispatch(incrementTime());
    }
  });
};



