import { Map } from 'immutable';
import { forEach } from 'ramda';
import { mapDiff, iterDiff } from './immutablediff';
import { iterateDepthFirst } from './iteration';
import THREE from 'three';
import { virtualCameraId, vrCameraId } from '../sceneGraph';
import { isSceneLoaded } from '../sceneIO';

const initialEvalSceneGraph = Map({
  loaded: false,
  nodes: Map(),
  references: Map(),
  referencesFrom: Map(),
  loadedFiles: Map(),
  fileReferences: Map(),
  scene: Map()
});

let doLog = false;
function log(...args) {
  if (doLog) console.log(...args);
}

function toPlugKey(plug, opIndex, key) { return `${plug}:${opIndex}:${key}` }

export function updateGraph(store, inputEvalSceneGraph = initialEvalSceneGraph) {
  // let start = new Date();
  let evalSceneGraph = inputEvalSceneGraph;

  const sceneId = store.getIn(['sceneIO', 'id']); // getSceneId
  if (!sceneId) return evalSceneGraph;

  const scene = store.get('sceneGraph');
  const prevScene = evalSceneGraph.get('scene');
  const loadedFiles = store.getIn(['sceneIO','loadedFiles']);
  const nowLoaded = !evalSceneGraph.get('loaded') && isSceneLoaded(store);
  const worldTransforms = {};

  const toUpdate = {};
  const toDelete = {};
  const updating = {};

  function getPlug(nodeId, sourcePlug, opIndex) {
    return (fromKey, id, destPlug) => {
      //console.log('getPlug: ', nodeId, sourcePlug, opIndex, fromKey, id, destPlug);
      if (updating[id]) {
        console.error('Circular reference, updating', scene.getIn([id, 'name']), 'from', scene.getIn([nodeId, 'name']))
        throw new Error('Circular reference');
      }
      if (toUpdate[id]) updateNode(id, toUpdate[id]);
      addReference(id, {id: nodeId, sourcePlug, destPlug}, toPlugKey(destPlug, opIndex, fromKey));
      var plugResult = evalSceneGraph.getIn(['nodes', id, destPlug]);
      return plugResult ? plugResult.set('id', id) : null;
    }
  };

  function getWorldTransform(id) {
    if (worldTransforms[id]) return worldTransforms[id];

    if (toUpdate[id] && !updating[id]) updateNode(id, toUpdate[id]);
    const localTransform = evalSceneGraph.getIn(['nodes', id, 'Transform', 'transform']);
    if (!localTransform) return null;

    const parent = scene.getIn([id, 'parent']);
    const parentTransform = parent && getWorldTransform(parent);
    worldTransforms[id] = parentTransform
      ? new THREE.Matrix4().copy(parentTransform).multiply(localTransform)
      : new THREE.Matrix4().copy(localTransform);
    return worldTransforms[id];
  }


  const properties = {
    Vec3: (val, key) => {
      return val && new THREE.Vector3(val.x, val.y, val.z);
    },

    Color: (val) => {
      return val && new THREE.Color(val.r, val.g, val.b);
    },

    Node: (val, key, nodeId, sourcePlug, opIndex) => {
      if (!val) return null;
      const id = val.get('id');
      if (updating[id]) throw new Error('Circular reference');
      if (toUpdate[id]) updateNode(id, toUpdate[id]);
      addReference(id, {id: nodeId, sourcePlug}, toPlugKey(sourcePlug, opIndex, key));
      return evalSceneGraph.getIn(['nodes', id])
    },

    Plug: (val, key, nodeId, sourcePlug, opIndex) => {
      if (!val) return null;
      return getPlug(nodeId, sourcePlug, opIndex)(key, val.get('id'), val.get('plug'));
    },

    Image: (val, key, nodeId, sourcePlug, opIndex) => {
      if (val) addFileReference(val.get('id'), {id: nodeId, sourcePlug}, toPlugKey(sourcePlug, opIndex, key));
      // console.log('image property', val && val.toJS());
      return val;
    },

    File: (val, key, nodeId, sourcePlug, opIndex) => {
      if (val) {
        addFileReference(val.get('id'), {id: nodeId, sourcePlug}, toPlugKey(sourcePlug, opIndex, key));
      }
      // console.log('image property', val && val.toJS());
      return val;
    },

    Binary: (val, key, nodeId, sourcePlug, opIndex) => {
      if (val) {
        addFileReference(val.get('id'), {id: nodeId, sourcePlug}, toPlugKey(sourcePlug, opIndex, key));
      }
      // console.log('image property', val && val.toJS());
      return val;
    },

    NodeList: (list, key, nodeId, sourcePlug, opIndex) => {
      //console.log('NodeList', val.toJS(), key, nodeId);
      if (!list) return null;
      const result = list.map((ref) => {
        const id = ref.get('id');
        if (updating[id]) throw new Error('Circular reference');
        if (toUpdate[id]) updateNode(id, toUpdate[id]);
        return evalSceneGraph.getIn(['nodes', id])
      });
      addReferences(list, {id: nodeId, sourcePlug}, toPlugKey(sourcePlug, opIndex, key));
      return result;
    },
  }


  function addFileReference(fileId, from, key) {
    if (!evalSceneGraph.getIn(['fileReferences', fileId])) evalSceneGraph = evalSceneGraph.setIn(['fileReferences', fileId], Map());
    evalSceneGraph = evalSceneGraph.setIn(['fileReferences', fileId, `${from.id}:${key}`], from);
  };

  function addReference(toId, from, key) {
    const oldRef = evalSceneGraph.getIn(['references', from.id, key]);
    if (oldRef && oldRef === toId) return;

    if (!evalSceneGraph.getIn(['references', from.id])) evalSceneGraph = evalSceneGraph.setIn(['references', from.id], Map());
    if (!evalSceneGraph.getIn(['referencesFrom', toId])) evalSceneGraph = evalSceneGraph.setIn(['referencesFrom', toId], Map());

    // console.log('addReference', toId, fromId, key, oldRefId, oldRefId === toId);

    if (oldRef) {
      evalSceneGraph = evalSceneGraph.deleteIn(['referencesFrom', oldRef, `${from.id}:${key}`]);
   }
    evalSceneGraph = evalSceneGraph.setIn(['referencesFrom', toId, `${from.id}:${key}`], from);
    evalSceneGraph = evalSceneGraph.setIn(['references', from.id, key], toId);
  }

  // Add a reference list
  function addReferences(toIds, from, key) {
    const oldRef = evalSceneGraph.getIn(['references', from.id, key]);
    if (oldRef && oldRef === toIds) return;

    if (!evalSceneGraph.getIn(['references', from.id])) evalSceneGraph = evalSceneGraph.setIn(['references', from.id], Map());

    toIds.forEach((toIdRef) => {
      const toId = toIdRef.get('id');
      if (!evalSceneGraph.getIn(['referencesFrom', toId])) evalSceneGraph = evalSceneGraph.setIn(['referencesFrom', toId], Map());
      evalSceneGraph = evalSceneGraph.setIn(['referencesFrom', toId, `${from.id}:${key}`], from);
    });

    evalSceneGraph = evalSceneGraph.setIn(['references', from.id, key], toIds);
  }

  function dgOp(op, nodeId, plug, opIndex) {
    return {
      get: function (key) {
        let type = op.typeOf(key);
        if (!properties[type]) {
          return op.get(key);
        }
        return properties[type](op.get(key), key, nodeId, plug, opIndex);
      },
      // FIXME: Operator param shouldn't be required, wny?
      mergeInto: function (operator, map) {
        var intermediate = {};
        for (var key of op.keys()) {
          intermediate[key] = this.get(key);
        }
        return map.merge(intermediate);
      },

      toJS: function() {
        return op.toJS();
      },

      getPlug: getPlug(nodeId, plug, opIndex),

      getWorldTransform, //: getWorldTransform(nodeId, plug, opIndex),
    }
  };

  function checkRefUpdate(id, plug) {
    let refs = evalSceneGraph.getIn(['referencesFrom', id])
    if (!refs) return;
    //console.log('checkRefUpdate', scene.getIn([id,'name']), plug);
    refs.forEach((ref, key) => {
      //console.log('  toUpdate?', scene.getIn([ref.id, 'name']), ref.sourcePlug, ref.destPlug, !toUpdate[ref.id], (!plug || plug===ref.destPlug), key);
      if (!plug || plug===ref.destPlug) {
        toUpdate[ref.id] = toUpdate[ref.id] ? true : ref.sourcePlug;
        checkRefUpdate(ref.id, ref.sourcePlug);
      }
    });
    //console.log('<--');
  }

  // if the file has changed, check for nodes to update.
  function checkFile(info) {
    let id = info.key;
    if (evalSceneGraph.getIn(['loadedFiles', id])) return;
    var refs = evalSceneGraph.getIn(['fileReferences', id]);
    if (!refs) return;
    refs.forEach((ref, key) => {
      if (!toUpdate[ref.id]) toUpdate[ref.id] = ref.sourcePlug;
      else toUpdate[ref.id] = true;

      checkRefUpdate(ref.id, ref.sourcePlug);
    });
  };

  function needsUpdate({type, key}) {
    //console.log('nedsUpdate: ', type, key, scene.getIn([key,'name']));
    if (type === 'ADD') {
      toUpdate[key] = true;
    } else if (type === 'CHANGE') {
      toUpdate[key] = true;
    } else {
      toDelete[key] = true;
    }
    checkRefUpdate(key);
  };


  function updateNode(id, updatePlug) {
    const nodeName = scene.getIn([id,'name']);

    //console.log('dg::updateNode', id, scene.getIn([id,'name']), updatePlug);
    updating[id] = true;
    const node = scene.get(id);
    const prevNode = prevScene.get(id);

    const prevResult = evalSceneGraph.getIn(['nodes', id]);
    let result = Map({id});
    let prevProperties = (prevResult && prevResult.get('Properties')) || Map();
    let properties = Map();

    function doUpdatePlug(ops, plug) {
      let doUpdate = updatePlug === true || plug === updatePlug || !prevNode || ops !== prevNode.getIn(['plugs', plug]);
      doUpdate = doUpdate || properties.get('visible') !== prevProperties.get('visible');
      //console.log(' - ', plug, updatePlug, doUpdate);

      if (doUpdate) {
        //console.log('updatePlug: ', node.get('name'), plug);
        result = result.set(plug, ops.reduce((result, op, idx) => {
          return op.doUpdate(dgOp(op, id, plug, idx), result, {sceneId, node, store, properties});
        }, Map()));
      } else {
        let prevPlugResult = prevResult ? prevResult.get(plug) : Map();
        //console.log('Force update', updatePlug === plug, plug);
        result = result.set(plug, (updatePlug === plug || updatePlug === true) ? prevPlugResult.set('__r', Math.random()) : prevPlugResult);
      }

      // Store the properties result, in order to pass to other operators.
      if (plug === 'Properties') properties = result.get('Properties');
    }

    if (node.getIn(['plugs','Properties'])) doUpdatePlug(node.getIn(['plugs','Properties']), 'Properties');
    if (node.getIn(['plugs','Transform'])) doUpdatePlug(node.getIn(['plugs','Transform']), 'Transform');

    node.get('plugs').forEach((ops, plug) => {
      if (plug !== 'Properties' && plug !== 'Transform') doUpdatePlug(ops, plug);
    });

    if (nodeName === 'Bone') {
      console.log('set bone node result into evalSceneGraph');
    }

    evalSceneGraph = evalSceneGraph.setIn(['nodes',id], result);

    delete updating[id];
    delete toUpdate[id];
  };

  function deleteNode(id) {
    evalSceneGraph = evalSceneGraph.delete(id);
    if (evalSceneGraph.getIn(['referencesFrom', id])) {
      evalSceneGraph = evalSceneGraph.deleteIn(['referencesFrom', id]);
    }
    if (evalSceneGraph.getIn(['references', id])) {
      evalSceneGraph = evalSceneGraph.deleteIn(['references', id]);
    }
  }

  forEach(checkFile, mapDiff(evalSceneGraph.get('loadedFiles'), loadedFiles));

  if (nowLoaded) {
    for (let node of iterateDepthFirst(scene, sceneId)) {
      let id = node.get('id');
      if (evalSceneGraph.getIn(['nodes', id, 'Properties', 'prefetch'])) {
        //console.log('Update from prefetch: ', node.get('name'));
        toUpdate[id] = node.get('type');
      }
    }
  }

  forEach(needsUpdate, iterDiff(evalSceneGraph.get('scene'), scene, sceneId, virtualCameraId));
  forEach(needsUpdate, iterDiff(evalSceneGraph.get('scene'), scene, sceneId, vrCameraId));

  for (let id of Object.keys(toDelete)) {
    deleteNode(id);
  }

  // console.log('evalSceneGraph updating: ', Object.keys(toUpdate).length);
  for (let id of Object.keys(toUpdate)) {
    if (toUpdate[id]) updateNode(id, toUpdate[id]);
  }

  if (nowLoaded) evalSceneGraph = evalSceneGraph.set('loaded', true);

  // Store the current files and sceneGraph used to calculate evalSceneGraph
  return evalSceneGraph.merge({scene, loadedFiles});
}

export function getEvalSceneGraph(store) {
  return store.getDerived('evalSceneGraph', updateGraph);
}




