export function iterateDepthFirstWithFilter(state, id, conditionalFn) {
  let node = id && state.get(id);
  // console.log('iterateDepthFirst?', id, node && node.get('name'));
  if (!node) return [];

  let index = -1;
  let children = node.get('children');
  let childIterator;

  let iterable = {
    [Symbol.iterator]() {
      let iterator = {
        next() {
          // console.log('next?', node.get('name'), index);
          if (index === -1) {
            index++;
            return {value: node};
          } else if (index < children.size) {
            if (childIterator) {
              let result = childIterator.next();
              if (!result.done) return result;
              else {
                childIterator = null;
                index++;
                return this.next();
              }
            } else {
              if (!conditionalFn || conditionalFn(state.get(children.get(index)))) {
                childIterator = iterateDepthFirstWithFilter(state, children.get(index))[Symbol.iterator]();
                return childIterator.next();
              } else {
                index++;
                return this.next();
              }
            }
          } else return {done: true};
        }
      };
      return iterator;
    }
  }
  return iterable;
}

export function iterateDepthFirst(state, id) {
  return iterateDepthFirstWithFilter(state, id);
}




