import THREE from 'three';
import rollbar from 'rollbar-browser';
import immutable from 'immutable'
import snabbdom from 'snabbdom';
import h from 'snabbdom/h';
import snabbdomClass from 'snabbdom/modules/class'; // makes it easy to toggle classes
import snabbdomProps from 'snabbdom/modules/props'; // for setting properties on DOM elements
import snabbdomAttrs from 'snabbdom/modules/attributes';
import snabbdomStyle from 'snabbdom/modules/style'; // handles styling on elements with support for animations
import snabbdomEvents from 'snabbdom/modules/eventlisteners'; // attaches event listeners
import eventEmitter from './utils/eventEmitter';
import ramda from 'ramda';
const { mapObjIndexed }  = ramda;

import callApi from './store/api';
import createLogger from 'redux-logger';
import clientMiddleware from './store/clientMiddleware';

import animation from './modules/animation';
import annotations from './modules/annotations';
import sceneGraph from './modules/sceneGraph';
import sceneIO from './modules/sceneIO';
import assets from './modules/assets';
import player from './modules/player';
import scene from './modules/scene';
import plugins from './modules/plugins';
import configuration from './modules/configuration';
import { setApi } from './modules/plugins';

import operatorRecords from './operators';
import primitives from './operators/primitives';

import buildStore from './store';
import bindActionCreatorsAndSelectors from './store/bindActionCreatorsAndSelectors';

import playerView from './components/player';

import tools from './translators/tools';
import threeTranslator from './translators/three';

import { addTool } from './modules/player';
import { startAutoRotate, autoRotateTool } from './autoRotate';

const Rollbar = __ROLLBAR_TOKEN__ && rollbar.init({
  accessToken: __ROLLBAR_TOKEN__,
  verbose: true,
  captureUncaught: true,
  payload: {
    environment: __ENV__,
    context: 'playerv2'
  }
});

const reducers = {
  animation: animation.reducer,
  annotations: annotations.reducer,
  assets: assets.reducer,
  sceneGraph: sceneGraph.reducer(operatorRecords),
  sceneIO: sceneIO.reducer,
  player: player.reducer,
  plugins: plugins.reducer,
};

// FIXME: remove this console.warn shim
//if (__DEV__) console.warn = function() { };

try {

const patch = snabbdom.init([snabbdomClass,snabbdomProps,snabbdomAttrs,snabbdomStyle,snabbdomEvents]);

// const middleware = [logger()];
const loggerMiddleware = createLogger({
  timestamp: true, duration: true, transformer: (state) => state.toJS()
});
let middleware = [clientMiddleware];
if (false) middleware.push(loggerMiddleware);

const deps = {
  immutable,
  patch,
  h,
  ramda,
  THREE
};


/**
 * The main function. This function initializes the player for a given DOM Node.
 *
 * @param {(String|DOMNode)} elOrId - the DOM Node or String id of a DOM Node.
 * @param {Function?} initializationFn - an optional initialization function that will be called (with api as argument) before the first render.
 * @returns {API} The clara playera API.
 * @public
 */
function claraplayer(elOrId, initializationFn) {
  const parentEl = typeof elOrId==='string' ? document.getElementById(elOrId) : elOrId;
  parentEl.appendChild(document.createElement('div'));
  const rect = parentEl.getBoundingClientRect();
  if (!rect.height) {
    throw new Error('Player element must have a height');
  }

  const store = buildStore(reducers, operatorRecords, primitives, middleware);
  if (Rollbar) store.useRollbar(Rollbar);
  let dispatch = store.dispatch.bind(store);
  const translator = threeTranslator(store);
  store.setTranslator(translator);
  store.setApi(callApi);


  const actions = bindActionCreatorsAndSelectors(store, {
    animation, annotations, assets, configuration, scene, sceneIO, sceneGraph, player, plugins
  });

  let api = Object.assign({}, eventEmitter(), {
    deps,
    three: {
      scene: translator.scene,
      camera: translator.camera,
    },
    _store: store
  }, actions);

  if (initializationFn && typeof initializationFn === 'function') initializationFn(api);

  const el = parentEl.children[0];
  var parentStyle = window.getComputedStyle(parentEl, null);
  const isFlex = parentStyle.display === 'flex';
  const isRelative = parentStyle.position === 'relative';
  let vnode = patch(el, playerView(store, { isFlex, isRelative, parentEl }));

  store.subscribe(function(events) { //try {
      vnode = patch(vnode, playerView(store, { isFlex, isRelative, parentEl }));
      if (events.length) events.forEach((e) => { api.emit(e.type, ...e.args); });
      api.emit('change');
    //} catch (e) {
    //  store.error(e);
    //}
  });

  dispatch(setApi(api));

  mapObjIndexed((tool, toolName) => dispatch(addTool(tool, toolName)), tools);
  const autoRotateToolObj = autoRotateTool(store);
  dispatch(addTool(autoRotateToolObj, 'stopAutoRotate'));

  api.on('loaded', () => {
    // TODO: zoom camera
    autoRotateToolObj.start(1000);
  });

  return api;
};

claraplayer.deps = deps;

claraplayer.hasWebGL = function() {
  const canvas = document.createElement('canvas');
  return !!canvas.getContext('webgl') || !!canvas.getContext('experimental-webgl');
};


module.exports = claraplayer;

} catch (e) {
  if (Rollbar) Rollbar.error(e);
}



