import THREE from 'three';
import clamp from './utils/clamp';
import updateCameraLookAtTarget from './utils/updateCameraLookAtTarget';
import getProjectionMatrix from './utils/getProjectionMatrix';
import deviceOrientationControls from './utils/deviceOrientationControls'
import { getWorldTransform, get, set, find } from '../modules/scene';
import { getCamera, getCameraAttrs, getInitialCameraPosition, setCameraPosition, setRect } from '../modules/player';
import { virtualCameraId } from '../modules/sceneGraph';
const { degToRad, radToDeg } = THREE.Math;
const { tan, cos, atan, acos, min, max, pow, PI, sqrt } = Math;

export default function initNavigator(store, initialBoundingBox, initialRect, initialCameraId) {

  const BOUNDING_BUFFER = 0.001;

  // values from outside
  let boundingBox = initialBoundingBox;
  let rect = initialRect;
  let cameraId = null;
  let orbitMode = null;
  let constraintCameraY = false;
  let pivotId = null;
  let maxBoundingSphere = null;
  let minBoundingSphere = new THREE.Sphere();
  let objectDisplay = false;

  // This is what we're adjusting
  let cameraWorldMatrix = null;
  let targetWorldMatrix = null;
  let pivotMatrix = null;
  let minFov = null;
  let minPolarAngle = 0;
  let maxPolarAngle = PI;

  let controls = deviceOrientationControls(new THREE.Object3D());

  // Temporary variables for intermediate calculations
  const cameraPosition = new THREE.Vector3();
  const targetPosition = new THREE.Vector3();
  const upPosition = new THREE.Vector3();
  const sphericalCamera = new THREE.Spherical();
  const sphericalTarget = new THREE.Spherical();
  const invTargetWorldMatrix = new THREE.Matrix4();
  const invPivotMatrix = new THREE.Matrix4();

  const initialMinPolarAngle = 0;
  const initialMaxPolarAngle = PI;
  const azimuthalAxis = new THREE.Vector3();

  var defaultCameraAttrs = getCameraAttrs(store).toJS();
  if (defaultCameraAttrs.azimuthAngle == null) defaultCameraAttrs.azimuthAngle = 45;
  if (defaultCameraAttrs.polarAngle == null) defaultCameraAttrs.polarAngle = 20;
  if (defaultCameraAttrs.boundingRadius == null) defaultCameraAttrs.boundingRadius = 1;

  function initCamera(newCameraId, dispatchless) {
    //if (cameraId === newCameraId) return;

    cameraId = newCameraId;
    cameraWorldMatrix = getWorldTransform(store, cameraId);

    azimuthalAxis.set(0,1,0);//.copy(upPosition);

    let playerData = get(store, { scene: true, evalPlug: 'Player' });
    let cameraData = get(store, { id:  cameraId, evalPlug: 'Camera' });
    let cameraTransform = get(store, { id:cameraId, evalPlug: 'Transform'});
    //console.log(cameraTransform);

    orbitMode=playerData.get('orbitMode');

    if (cameraTransform.get('target')){
      targetWorldMatrix = new THREE.Matrix4().copyPosition(getWorldTransform(store, cameraTransform.getIn(['target', 'id'])));
    } else if (playerData.get('orbitTarget')) {
      pivotId = playerData.getIn(['orbitTarget','id']);
      targetWorldMatrix = new THREE.Matrix4().copyPosition(getWorldTransform(store, playerData.getIn(['orbitTarget','id'])));
    } else {
      pivotId = null;
      const bbCenter = boundingBox.center();
      //console.log( 'bb center target', bbCenter, JSON.stringify( state.boundingBox ) );
      targetWorldMatrix = new THREE.Matrix4().makeTranslation( bbCenter.x, bbCenter.y, bbCenter.z );
    }
    pivotMatrix = targetWorldMatrix.clone();
    const pivotPosition = new THREE.Vector3().setFromMatrixPosition(pivotMatrix);
    cameraPosition.setFromMatrixPosition(cameraWorldMatrix).sub(pivotPosition);

    upPosition.set(0,1,0);
    if(orbitMode==='trackball'){
      upPosition.transformDirection(cameraWorldMatrix);
      const offset = cameraPosition.length();
      const normal = new THREE.Vector3().copy(cameraPosition).normalize();
      const cameraViewPlane = new THREE.Plane(normal, 0);
      upPosition.copy(cameraViewPlane.projectPoint(upPosition).normalize());
    }

    const pivY = pivotMatrix.elements[13];
    if (pivY+cameraPosition.length()<0 && constraintCameraY) {
      cameraPosition.normalize().multiplyScalar(pivY);
    }
    cameraWorldMatrix.setPosition(cameraPosition.applyMatrix4(pivotMatrix));

    removeScale(targetWorldMatrix);

    resize(rect);

    adjustAngleAndRadius(cameraId === virtualCameraId ? defaultCameraAttrs : getCameraAttrs(store).toJS());

    // update orientation
    constraintCameraY = playerData.get('constraintCameraY');
    if (constraintCameraY) updateSphericalRotation(new THREE.Vector2(0,0), dispatchless);
    updateCameraLookAtTarget(cameraWorldMatrix, targetWorldMatrix, upPosition);

    if(store.getIn(['player', 'constrainCameraRadius'])) enableObjectMode(store.getIn(['player', 'constrainCameraRadius']));

    if(!dispatchless){
      store.dispatch(setCameraPosition({
        cameraWorldMatrix: cameraWorldMatrix.clone(),
        initialCameraWorldMatrix: cameraWorldMatrix.clone(),
        initialTargetWorldMatrix: targetWorldMatrix.clone(),
      }));
    } else {
      return cameraWorldMatrix.clone();
    }
  };

  function switchCamera(newCameraId) {
    initCamera(newCameraId);
  }

  function changeCameraPosition(pos) {
    cameraWorldMatrix.setPosition(pos);
    store.dispatch(setCameraPosition({
      cameraWorldMatrix: cameraWorldMatrix.clone(),
    }));
  }

  function adjustAngleAndRadius({ polarAngle, azimuthAngle, boundingRadius }) {
    if (azimuthAngle != null && polarAngle != null) {
      invTargetWorldMatrix.getInverse(targetWorldMatrix, true);
      // world to relative
      cameraPosition.setFromMatrixPosition(cameraWorldMatrix).applyMatrix4(invTargetWorldMatrix);

      sphericalCamera.setFromVector3(cameraPosition);
      sphericalCamera.theta = degToRad(azimuthAngle);
      sphericalCamera.phi = degToRad(90 - polarAngle);

      minPolarAngle = max(minPolarAngle || 0, initialMinPolarAngle); // radians
      maxPolarAngle = min(maxPolarAngle || PI, initialMaxPolarAngle); // radians

      sphericalCamera.phi = clamp(sphericalCamera.phi, minPolarAngle, maxPolarAngle);

      if (boundingRadius != null) {
        const fixRadius = boundingRadius * boundingBox.getBoundingSphere().radius;
        sphericalCamera.radius = (fixRadius <= 0 ? 1 : fixRadius) / tan(degToRad(minFov * 0.5));
      }

      sphericalCamera.makeSafe();

      cameraPosition.setFromSpherical(sphericalCamera);
      sphericalCamera.setFromVector3(cameraPosition);

      // relative to world
      cameraPosition.applyMatrix4(targetWorldMatrix);
      cameraWorldMatrix.setPosition(cameraPosition);
    }
  }

  function reset() { //{ initialCameraWorldMatrix, initialTargetWorldMatrix }) {
    initCamera(cameraId);
    /*const cameraAttrs = getInitialCameraPosition(store);
    targetWorldMatrix = cameraAttrs.initialTargetWorldMatrix.clone();
    cameraWorldMatrix = cameraAttrs.initialCameraWorldMatrix.clone();
    store.dispatch(setCameraPosition({
      cameraWorldMatrix: cameraAttrs.initialCameraWorldMatrix.clone()
    }));*/
  }

  function getCameraWorldMatrix() {
    return cameraWorldMatrix;
  };

  function setCameraWorldMatrix(m) {
    cameraWorldMatrix.copy(m);
  }

  function getTargetWorldMatrix() {
    return targetWorldMatrix;
  };

  function getBoundingBox(){
    return boundingBox;
  }

  function resize(viewportSize) {
    const cameraData = cameraId && get(store, { id: cameraId, evalPlug: 'Camera' });
    const yFov = cameraData ? cameraData.get('fieldOfView') : 45;
    const initialAspect = cameraData ? cameraData.get('aspectRatio') : 16/9;

    rect = viewportSize;

    const aspectRatio = rect.x / rect.y;

    minFov = yFov;

    store.dispatch(setRect({width: rect.x, height: rect.height}));
    if (cameraId) store.dispatch(set({ id: cameraId, plug: 'Camera', properties: { name: 'Camera' }}, { aspectRatio, fieldOfView: yFov }));
  }

  function updateSphericalRotation(mouseDelta, dispatchless) {
    if (orbitMode==='trackball') trackballUpdate(mouseDelta, dispatchless);
    else if (orbitMode==='gyroscope') gyroscropeUpdate(mouseDelta, dispatchless);
  }

  function gyroscropeUpdate({ x, y }, dispatchless) {
    //to target space
    targetPosition.setFromMatrixPosition(targetWorldMatrix);
    cameraPosition.setFromMatrixPosition(cameraWorldMatrix).sub(targetPosition);

    let cameraSpherical = new THREE.Spherical().setFromVector3(cameraPosition);

    let azimuthalAngle=2*PI*(-x/rect.x);
    cameraSpherical.theta += azimuthalAngle;

    let polarAngle=2*PI*(-y/rect.y);
    cameraSpherical.phi += polarAngle;
    let maxPolarAngle;
    if(targetPosition.y/cameraPosition.length() <= 1){
      maxPolarAngle = constraintCameraY ? acos(-targetPosition.y/cameraPosition.length()) : PI;
    } else {
      maxPolarAngle = PI;
    }
    cameraSpherical.phi = clamp(cameraSpherical.phi, 0, maxPolarAngle);

    cameraSpherical.makeSafe();
    cameraPosition.setFromSpherical(cameraSpherical).add(targetPosition);

    //bounding box constraining
    if (objectDisplay && maxBoundingSphere){
      const camToTarg = new THREE.Vector3().subVectors(targetPosition, cameraPosition);
      if (minBoundingSphere.containsPoint(cameraPosition)){
        const backwardRay = new THREE.Ray(cameraPosition, camToTarg.multiplyScalar(-1));
        const newPosition = backwardRay.intersectSphere(minBoundingSphere).addScaledVector(camToTarg, BOUNDING_BUFFER);
        cameraPosition.copy(newPosition);
      } else if (!maxBoundingSphere.containsPoint(cameraPosition)){
        const forwardRay = new THREE.Ray(cameraPosition, camToTarg);
        const newPosition = forwardRay.intersectSphere(maxBoundingSphere).addScaledVector(camToTarg, BOUNDING_BUFFER);
        cameraPosition.copy(newPosition);
      } else {}
    }


    cameraWorldMatrix.setPosition(cameraPosition);

    // update orientation
    updateCameraLookAtTarget( cameraWorldMatrix, targetWorldMatrix, upPosition );
    if(!dispatchless){
      store.dispatch(setCameraPosition({
        cameraWorldMatrix: cameraWorldMatrix.clone(),
      }));
    }
  }

  function trackballUpdate({ x, y }, dispatchless){
    const radius = min(rect.x, rect.y);
    const pivotPosition = new THREE.Vector3().setFromMatrixPosition(pivotMatrix);

    let relativeDelta = new THREE.Vector3().set(-2*x/radius,2*y/radius,0);
    if (relativeDelta.length()>1) relativeDelta.normalize();
    if (relativeDelta.x===0 && relativeDelta.y===0) return;
    let delta = relativeDelta.length();

    //world to relative pivot space
    relativeDelta.transformDirection(cameraWorldMatrix).multiplyScalar(delta);
    cameraPosition.setFromMatrixPosition(cameraWorldMatrix).sub(pivotPosition);
    targetPosition.setFromMatrixPosition(targetWorldMatrix).sub(pivotPosition);
    //upPosition.transformDirection(invPivotMatrix);
    let camNorm = new THREE.Vector3().copy(cameraPosition).normalize();
    //create rotational Quaternion
    const scaleFactor = sqrt(1-relativeDelta.lengthSq());
    relativeDelta.addScaledVector(camNorm, scaleFactor).normalize();

    let rotationalAxis = new THREE.Vector3().crossVectors(camNorm,relativeDelta).normalize();
    let rotationalAngle = camNorm.angleTo(relativeDelta);
    let rotationalQuaternion = new THREE.Quaternion().setFromAxisAngle(rotationalAxis, rotationalAngle);


    //pivot relative to world space
    cameraPosition.applyQuaternion(rotationalQuaternion).add(pivotPosition);
    targetPosition.applyQuaternion(rotationalQuaternion).add(pivotPosition);
    upPosition.applyQuaternion(rotationalQuaternion)//.transformDirection(pivotMatrix);

    //bounding box constraining
    if (objectDisplay && maxBoundingSphere){
      const camToTarg = new THREE.Vector3().subVectors(targetPosition, cameraPosition);

      if (minBoundingSphere.containsPoint(cameraPosition)){
        const backwardRay = new THREE.Ray(cameraPosition,
          new THREE.Vector3().copy(camToTarg).multiplyScalar(-1));

        const newPosition = backwardRay.intersectSphere(minBoundingSphere)
          .addScaledVector(camToTarg, -BOUNDING_BUFFER);

        cameraPosition.copy(newPosition);
      } else if (!maxBoundingSphere.containsPoint(cameraPosition)){
        const forwardRay = new THREE.Ray(cameraPosition, camToTarg);

        const newPosition = forwardRay.intersectSphere(maxBoundingSphere)
          .addScaledVector(camToTarg, BOUNDING_BUFFER);

        cameraPosition.copy(newPosition);
      } else {}

      if (!minBoundingSphere.containsPoint(targetPosition)){
        const thirdVector = new THREE.Vector3().crossVectors(camToTarg, upPosition);
        const newPosition = minBoundingSphere.clampPoint(targetPosition);

        const posChange = new THREE.Vector3().subVectors(newPosition,targetPosition);
        newPosition.addScaledVector(posChange, BOUNDING_BUFFER);

        upPosition.crossVectors(thirdVector,
          camToTarg.subVectors(newPosition, cameraPosition)).normalize();
        targetPosition.copy(newPosition);
      }
    }

    cameraWorldMatrix.setPosition(cameraPosition);
    targetWorldMatrix.setPosition(targetPosition);

    updateCameraLookAtTarget( cameraWorldMatrix, targetWorldMatrix, upPosition );

    if(!dispatchless){
      store.dispatch(setCameraPosition({
        cameraWorldMatrix: cameraWorldMatrix.clone(),
      }));
    }
  }

  const updatePan = function() {
    const cameraProjectMatrix = new THREE.Matrix4();
    const cameraUnprojectMatrix = new THREE.Matrix4();
    const ndcReferencePoint = new THREE.Vector3();
    const worldRelative = new THREE.Vector3();
    const deltaReferencePoint = new THREE.Vector3();
    const referencePoint = new THREE.Vector3();
    const translationMatrix = new THREE.Matrix4();

    return (mouseDelta) => {
      var projectionMatrix = getProjectionMatrix(get(store, {id: cameraId, evalPlug: 'Camera' }));
      var relativeDelta = new THREE.Vector2().copy(mouseDelta).divide(rect).multiplyScalar(2.0);
      relativeDelta.x *= -1;

      const invCameraWorldMatrix = new THREE.Matrix4().getInverse(cameraWorldMatrix, true);
      cameraProjectMatrix.copy(projectionMatrix).multiply(invCameraWorldMatrix);

      ndcReferencePoint.setFromMatrixPosition(targetWorldMatrix).applyProjection(cameraProjectMatrix);
      cameraUnprojectMatrix.getInverse(cameraProjectMatrix, true);

      referencePoint.set(0, 0, ndcReferencePoint.z).applyProjection(cameraUnprojectMatrix);
      deltaReferencePoint.set(relativeDelta.x, relativeDelta.y, ndcReferencePoint.z).applyProjection(cameraUnprojectMatrix);

      // ensure that NDC has the same layout as mouseDelta coordinate space
      worldRelative.copy(deltaReferencePoint).sub(referencePoint);

      //bounding box constraining
      if (objectDisplay && maxBoundingSphere && worldRelative.length()>0){
        cameraPosition.setFromMatrixPosition(cameraWorldMatrix);
        targetPosition.setFromMatrixPosition(targetWorldMatrix);

        const directionNorm = new THREE.Vector3().copy(worldRelative).normalize();
        const camRay = new THREE.Ray(cameraPosition, directionNorm);
        const targRay = new THREE.Ray(targetPosition, directionNorm);

        const targHit = targRay.intersectSphere(minBoundingSphere).sub(targetPosition).length()-BOUNDING_BUFFER;

        let inCamHit = camRay.intersectSphere(minBoundingSphere);
        if (inCamHit) inCamHit = inCamHit.sub(cameraPosition).length()-BOUNDING_BUFFER;
        else inCamHit = Infinity;

        const outCamHit = camRay.intersectSphere(maxBoundingSphere).sub(cameraPosition).length()-BOUNDING_BUFFER;

        const moveLength = Math.min(worldRelative.length(), targHit, inCamHit, outCamHit);
        worldRelative.normalize().multiplyScalar(moveLength);
      }

      translationMatrix.makeTranslation(worldRelative.x, worldRelative.y, worldRelative.z);
      targetWorldMatrix.multiplyMatrices(translationMatrix, targetWorldMatrix);
      cameraWorldMatrix.multiplyMatrices(translationMatrix, cameraWorldMatrix);

      if (constraintCameraY && orbitMode!=='trackball') {
        cameraPosition.setFromMatrixPosition(cameraWorldMatrix);
        targetPosition.setFromMatrixPosition(targetWorldMatrix);
        if (cameraPosition.y<0) {
          targetPosition.y -= cameraPosition.y;
          cameraPosition.y = 0;
        }
        targetWorldMatrix.setPosition(targetPosition);
        cameraWorldMatrix.setPosition(cameraPosition);
      }

      updateCameraLookAtTarget(cameraWorldMatrix, targetWorldMatrix, upPosition);

      store.dispatch(setCameraPosition({
        cameraWorldMatrix: cameraWorldMatrix.clone(),
      }));
    }
  }();

  const updateZoom = function() {
    const EPS = 0.000001;
    const targetPosition = new THREE.Vector3();
    const targetToCamera = new THREE.Vector3();

    return (mouseDelta, minZoomDistance, maxZoomDistance) => {
      const deltaZoom = mouseDelta / rect.y;
      const decayZoomFactor = pow(2, deltaZoom * 100);  // this ensures the zoom has consistent speed no matter the size

      cameraPosition.setFromMatrixPosition(cameraWorldMatrix);
      targetPosition.setFromMatrixPosition(targetWorldMatrix);

      targetToCamera.copy(cameraPosition).sub(targetPosition);
      const targetToCameraDistance = max(targetToCamera.length(), EPS);
      let newTargetToCameraDistance = targetToCameraDistance * decayZoomFactor;


      if (objectDisplay && maxBoundingSphere){
        const camToTargetBackward = new THREE.Vector3().copy(targetToCamera).normalize();
        const camToTargetForward = new THREE.Vector3().copy(camToTargetBackward).multiplyScalar(-1);

        const forwardRay = new THREE.Ray(cameraPosition,camToTargetForward);
        const backwardRay = new THREE.Ray(cameraPosition,camToTargetBackward);

        let lowerBound = new THREE.Vector3(), upperBound = new THREE.Vector3();

        forwardRay.intersectSphere(minBoundingSphere, lowerBound);
        backwardRay.intersectSphere(maxBoundingSphere, upperBound);

        const minZoom = lowerBound.sub(targetPosition).length();
        const maxZoom = upperBound.sub(targetPosition).length();

        newTargetToCameraDistance = clamp(newTargetToCameraDistance, minZoom+BOUNDING_BUFFER, maxZoom-BOUNDING_BUFFER);
      }

      targetToCamera.multiplyScalar(newTargetToCameraDistance / targetToCameraDistance);
      cameraPosition.copy(targetPosition).add(targetToCamera);
      if (cameraPosition.y<0 && constraintCameraY && orbitMode!=='trackball') {
        const thirdVector = new THREE.Vector3().crossVectors(targetToCamera, upPosition).normalize();
        cameraPosition.y=0;
        targetToCamera.copy(cameraPosition).sub(targetPosition);
      }
      cameraWorldMatrix.setPosition(cameraPosition);

      updateCameraLookAtTarget(cameraWorldMatrix, targetWorldMatrix, upPosition);

      store.dispatch(setCameraPosition({
        cameraWorldMatrix: cameraWorldMatrix.clone(),
      }));
    };
  }();

  function updatePivot(){
    let cameraTransform = get(store, { id:cameraId, evalPlug: 'Transform'});
    let playerData = get(store, { scene: true, evalPlug: 'Player' });
    const playerTarget = playerData.get('orbitTarget');
    const lookAtTarget = cameraTransform.get('target');
    let target = playerTarget;
    if(lookAtTarget) target = lookAtTarget;

    const camMove = playerTarget && !lookAtTarget;
    if (target) {
      const newPivotPosition = new THREE.Vector3().setFromMatrixPosition(getWorldTransform(store, target.get('id')));
      const pivotPosition = new THREE.Vector3().setFromMatrixPosition(pivotMatrix);
      const delta = new THREE.Vector3().subVectors(newPivotPosition, pivotPosition);

      if(camMove) cameraPosition.setFromMatrixPosition(cameraWorldMatrix);
      targetPosition.setFromMatrixPosition(targetWorldMatrix);
      if(camMove) cameraPosition.add(delta);
      targetPosition.add(delta);
      if(camMove) cameraWorldMatrix.setPosition(cameraPosition);
      targetWorldMatrix.setPosition(targetPosition);
      pivotMatrix.setPosition(newPivotPosition);

      if(delta.length()>0){
        updateCameraLookAtTarget(cameraWorldMatrix, targetWorldMatrix, upPosition);
        updateSphericalRotation(new THREE.Vector2(0,0));
        store.dispatch(setCameraPosition({
          cameraWorldMatrix: cameraWorldMatrix.clone(),
        }));
      }
    }
  }

  function enableObjectMode(outerRadius){
    boundingBox.getBoundingSphere(minBoundingSphere);
    if (outerRadius<=minBoundingSphere.radius) {
      console.warn("Outer bounding sphere too small");
      return;
    }
    maxBoundingSphere = new THREE.Sphere(minBoundingSphere.center, outerRadius);
    objectDisplay = maxBoundingSphere!==null;

    targetPosition.setFromMatrixPosition(targetWorldMatrix);
    cameraPosition.setFromMatrixPosition(cameraWorldMatrix);
    const targetToCamera = new THREE.Vector3().subVectors(cameraPosition, targetPosition);

    const thirdVector = new THREE.Vector3().crossVectors(targetToCamera, upPosition).normalize();
    const newTargetPosition = minBoundingSphere.clampPoint(targetPosition);
    const newCameraPosition = maxBoundingSphere.clampPoint(cameraPosition);
    cameraWorldMatrix.setPosition(newCameraPosition);
    targetWorldMatrix.setPosition(newTargetPosition);
    if(orbitMode==='trackball') {
      const newTargetToCamera = new THREE.Vector3().subVectors(newCameraPosition, newTargetPosition);
      upPosition.crossVectors(thirdVector, newTargetToCamera).normalize();
    }
    updateZoom(0);
  }

  function disableObjectMode(){
    maxBoundingSphere = null;
    objectDisplay = false;
  }

  function updateDeviceOrientation(ev){
    if(controls.freeze)return;
    controls.update(ev);

    const scale = new THREE.Vector3();
    cameraWorldMatrix.decompose(cameraPosition, new THREE.Quaternion(), scale);

    targetPosition.setFromMatrixPosition(targetWorldMatrix);
    const distToTarget = targetPosition.distanceTo(cameraPosition);
    const newPosition = new THREE.Vector3(0, 0, -1).applyQuaternion(controls.object.quaternion);
    newPosition.multiplyScalar(distToTarget).add(cameraPosition);
    targetWorldMatrix.setPosition(newPosition);

    cameraWorldMatrix.compose(cameraPosition, controls.object.quaternion, scale);

    store.dispatch(setCameraPosition({
      cameraWorldMatrix: cameraWorldMatrix.clone()
    }));
  }

  function enableHeadTracking(){
    controls.connect();
  }

  function disableHeadTracking(){
    controls.disconnect();
  }

  const rotateNode = function(){
    const parentInvMatrix = new THREE.Matrix4();
    const nodeRotationQuaternion = new THREE.Quaternion();
    const nodePosition = new THREE.Vector3();
    const nodeScale = new THREE.Vector3();
    const nodeRotationEuler = new THREE.Euler();
    return ({x,y}) => {
      const manipId = store.getState().getIn(['player', 'manipulationId']) || pivotId;
      const nodeTransform = getWorldTransform(store, manipId)
      if (!nodeTransform) return;

      nodeTransform.decompose(nodePosition, nodeRotationQuaternion, nodeScale);
      cameraPosition.setFromMatrixPosition(cameraWorldMatrix);

      const radius = min(rect.x, rect.y);

      let relativeDelta = new THREE.Vector3().set(2*x/radius,-2*y/radius,0);
      if (relativeDelta.length()>1) relativeDelta.normalize();
      else if (relativeDelta.length()===0) return;
      let delta = relativeDelta.length();
      relativeDelta.transformDirection(cameraWorldMatrix).multiplyScalar(delta);

      const objNormal = new THREE.Vector3().subVectors(cameraPosition,nodePosition).normalize();
      const scaleFactor = sqrt(1-relativeDelta.lengthSq());
      relativeDelta.addScaledVector(objNormal, scaleFactor).normalize();
      const modifyQuaternion = new THREE.Quaternion().setFromUnitVectors(objNormal, relativeDelta);
      nodeRotationQuaternion.premultiply(modifyQuaternion);

      nodeTransform.compose(nodePosition, nodeRotationQuaternion, nodeScale);
      const parentMatrix = getWorldTransform(store, find(store, {id: manipId, parent:true}));
      if(parentMatrix) {
        parentInvMatrix.getInverse(parentMatrix, true);
        nodeTransform.premultiply(parentInvMatrix);
      }
      nodeTransform.decompose(nodePosition, nodeRotationQuaternion, nodeScale);

      nodeRotationEuler.setFromQuaternion(nodeRotationQuaternion, 'ZYX');
      store.dispatch(set({id:manipId, plug:'Transform', property:'rotation'}, nodeRotationEuler.toVector3().multiplyScalar(180/PI)));
    }
  }();

  const moveNode = function (){
    const invCameraWorldMatrix = new THREE.Matrix4();
    const cameraProjectMatrix = new THREE.Matrix4();
    const cameraUnprojectMatrix = new THREE.Matrix4();
    const inverseParent = new THREE.Matrix4();
    const referencePoint = new THREE.Vector3();
    const deltaReferencePoint = new THREE.Vector3();
    return (x,y) => {
      const manipId = store.getState().getIn(['player', 'manipulationId']) || pivotId;
      var projectionMatrix = getProjectionMatrix(get(store, {id: cameraId, evalPlug: 'Camera' }));
      invCameraWorldMatrix.getInverse(cameraWorldMatrix, true);
      cameraProjectMatrix.copy(projectionMatrix).multiply(invCameraWorldMatrix);

      const nodeMatrix = getWorldTransform(store, manipId);
      if(!nodeMatrix) return;

      const parentMatrix = getWorldTransform(store, find(store, {id:manipId, parent:true}));
      const nodePosition = new THREE.Vector3().setFromMatrixPosition(nodeMatrix);
      var relativeDelta = new THREE.Vector2(x,-y).divide(rect).multiplyScalar(2.0);

      referencePoint.copy(nodePosition).applyProjection(cameraProjectMatrix);
      referencePoint.x = 0;
      referencePoint.y = 0;
      deltaReferencePoint.set(relativeDelta.x,relativeDelta.y,referencePoint.z);

      cameraUnprojectMatrix.getInverse(cameraProjectMatrix,true);
      referencePoint.applyProjection(cameraUnprojectMatrix);
      deltaReferencePoint.applyProjection(cameraUnprojectMatrix).sub(referencePoint);
      nodePosition.add(deltaReferencePoint);

      nodeMatrix.setPosition(nodePosition);
      if(parentMatrix) nodeMatrix.premultiply(inverseParent.getInverse(parentMatrix));
      nodePosition.setFromMatrixPosition(nodeMatrix)

      store.dispatch(set({id:manipId, plug:'Transform', property:'translation'}, nodePosition));
    }
  }();

  function scaleNode(delta){
    const EPS = 0.000001;
    const deltaZoom = delta/rect.y;
    const decayZoomFactor = pow(2, deltaZoom * 100);

    const manipId = store.getState().getIn(['player', 'manipulationId']) || pivotId;
    const nodeGraphScale = get(store,{id:manipId, plug:'Transform', property:'scale'});
    if(!nodeGraphScale) return;

    const nodeScale = new THREE.Vector3().copy(nodeGraphScale);
    const oldScale = max(nodeScale.length(), EPS);
    const newScale = oldScale*decayZoomFactor;
    nodeScale.setLength(newScale);
    store.dispatch(set({id:manipId, plug:'Transform', property:'scale'}, nodeScale));
  }

  if (initialCameraId) initCamera(initialCameraId);

  return {
    initCamera,
    switchCamera,
    resize,
    reset,
    getCameraWorldMatrix,
    getTargetWorldMatrix,
    changeCameraPosition,
    getBoundingBox,
    updateSphericalRotation,
    updatePan,
    updateZoom,
    updatePivot,
    updateDeviceOrientation,
    enableHeadTracking,
    disableHeadTracking,
    rotateNode,
    moveNode,
    scaleNode,
    enableObjectMode,
    disableObjectMode
  };
};

function removeScale(matrix) {
  const position = new THREE.Vector3();
  const quaternion = new THREE.Quaternion();
  const scale = new THREE.Vector3();
  matrix.decompose(position, quaternion, scale);
  matrix.compose(position, quaternion, new THREE.Vector3(1, 1, 1) );
}



