import { iterateDepthFirst } from './iteration';

export function mapDiff(prev, next) {
  var ops = [];
  if (prev === next) return ops;

  prev.forEach((prevValue, key) => {
    if (next.has(key)) {
      let nextValue = next.get(key);
      if (nextValue !== prevValue) {
        ops.push({type: 'CHANGE', key, from: prevValue, to: nextValue});
      }
    } else {
      ops.push({type: 'REMOVE', key, value: prevValue});
    }
  });

  next.forEach((value, key) => {
    if (!prev.has(key)) {
      ops.push({type: 'ADD', key, value})
    }
  });

  return ops;
}

export function mapIterable(prev, next, iteratorFn) {
  var ops = [];
  if (prev === next) return ops;

  for (let prevNode of iteratorFn(prev)) {
    let id = prevNode.get('id');

    if (next.has(id)) {
      let nextNode = next.get(id);
      if (nextNode !== prevNode) {
        ops.push({type: 'CHANGE', id, from: prevNode, to: nextNode});
      }
    } else {
      ops.push({type: 'REMOVE', id, node: prevNode});
    }
  }

  for (let node of iteratorFn(next)) {
    let id = node.get('id');
    if (!prev.has(id)) {
      ops.push({type: 'ADD', id, node})
    }
  }

  return ops;
}

export function iterDiff(prevSceneGraph, nextSceneGraph, sceneId, cameraId) {
  let ops = [];

  for (let prevNode of iterateDepthFirst(prevSceneGraph, sceneId)) {
    const id = prevNode.get('id');
    const nextNode = nextSceneGraph.get(id);

    if (nextNode) {
      if (nextNode !== prevNode)ops.push({ type: 'CHANGE', key: id, from: prevNode, to: nextNode });
    } else {
      ops.push({ type: 'REMOVE', key: id, value: prevNode });
    }
  }

  for (let nextNode of iterateDepthFirst(nextSceneGraph, sceneId)) {
    const id = nextNode.get('id');
    if (!prevSceneGraph.get(id)) {
      ops.push({type: 'ADD', key: id, node: nextNode});
    }
  }

  if (cameraId) {
    const nextCameraNode = nextSceneGraph.get(cameraId);
    const prevCameraNode = prevSceneGraph.get(cameraId);
    if (prevCameraNode) {
      if (nextCameraNode) {
        ops.push({ type: 'CHANGE', key: cameraId, from: prevCameraNode, to: nextCameraNode });
      } else {
        ops.push({ type: 'REMOVE', key: cameraId, value: prevCameraNode });
      }
    } else if (nextCameraNode) {
      ops.push({ type: 'ADD', key: cameraId, node: nextCameraNode });
    }
  }

  return ops;
}



