import { curry, map } from 'ramda';
import { getOrFetchImage, getOrFetchBinary } from '../modules/assets';
import createTranslator from './translator';


export function getAssetIdFromImage(store, image) {
  const ref = image.get('asset');
  const hdr = image.get('hdr');
  return image.get('preferHDR') && hdr ? hdr.get('id') : ref.get('id');
};

export function getImageFromNode(store, image) {
  const id = getAssetIdFromImage(store, image);
  return id && getOrFetchImage(id)(store);
}


export function fetchTextureImage(store, image) {
  const ref = image.get('asset');
  const id = ref && ref.get('id');
  return id && getOrFetchImage(id)(store);
}

// Asset translator!
function translate(store, state={}, id, evalNode, prevEvalNode, node, prevNode) {
  if (!node) return state; // deleted node, do not care
  let type = node.get('type');
  if (translators[type]) translators[type](store, state, id, evalNode, prevEvalNode, node, prevNode);
  return state;
};


const translators = {
  PolyMesh: (store, state, id, data, prevData, node, prevNode) => {
    if (!data.getIn(['Properties','visible'])) return;
    const ref = data.getIn(['PolyMesh','geometry']);
    const bingeom = ref && getOrFetchBinary(ref.get('id'))(store);
    fetchMaterial(store, data.get('Material'));
  },

  BinMesh: (store, state, id, data, prevData, node, prevNode) => {
    if (!data.getIn(['Properties','visible'])) return;
    const ref = data.getIn(['BinMesh','binary']);
    const bingeom = ref && getOrFetchBinary(ref.get('id'))(store);
    fetchMaterial(store, data.get('Material'));
  },
};

const imageMaps = [
 'baseMap','baseFalloffMap',
 'anisotropyMap','anisotropyRotationMap','bumpMap','emissiveMap','lightMap',
 'metallicMap','normalMap','opacityMap','reflectivityMap','roughnessMap',
 'specularMap','translucencyMap',
];

function fetchMaterial(store, data) {
  for (var image of imageMaps) {
    fetchTextureImages(store, data.get(image));
  }
  fetchTextureImages(store, data.getIn(['envMap', 'specularCubeMap']));
  fetchTextureImages(store, data.getIn(['envMap', 'irradianceCubeMap']));
};

function fetchTextureImages(store, image) {
  if (!image) return null;
  const maps = image.get('maps');
  if (maps) {
    return map(curry(getImageFromNode)(store), maps);
  } else {
    fetchTextureImage(store, image);
  }
}

