import THREE from 'three';
import { manipulators } from '../modules/player';

function isManip(store, key) {
  return store.getIn(['player','activeManipulator']) === manipulators[key];
};

function isManipOn(store, key) {
  return store.getIn(['player', 'enableTools', key]);
}

const tools = {
  orbit: (store, { navigator }) => {
    return {
      drag: function(ev) {
        if (ev.which !== 1 || !isManip(store, 'orbit') || !isManipOn(store, 'orbit')) return false;

        return {
          momentum: true,
          handle: function({ deltaX, deltaY }) {
            navigator.updateSphericalRotation(new THREE.Vector2(deltaX, deltaY));
          }
        };
      }
    };
  },

  pan: (store, { navigator}) => {
    function move({ deltaX, deltaY }) {
      navigator.updatePan(new THREE.Vector2(deltaX, deltaY));
    }

    return {
      drag: function(ev) {
        if ((!isManip(store, 'pan') && ev.which !== 2 && ev.which !== 3) || !isManipOn(store, 'pan')) return false;

        return {
          momentum: true,
          handle: function(ev) {
            move(ev);
          }
        };
      },
      swipe: function(ev) {
        if (!isManipOn(store, 'pan')) return false;
        move(ev);
      }
    };
  },

  zoom: (store, state) => {
    function zoomBy({ delta, deltaX, deltaY }) {
      const d = delta !== undefined ? delta : ((deltaX / 30) + (-deltaY / 30));

      state.navigator.updateZoom(-d);
    }

    return {
      drag: function(ev) {
        if (!isManip(store, 'zoom') || !isManipOn(store, 'zoom')) return false;

        return {
          momentum: true,
          handle: function(ev) {
            const newEv = {delta: ev.delta !== undefined ? ev.delta : ((ev.deltaX / 30) + (-ev.deltaY / 30))};
            zoomBy(newEv);
          }
        };
      },

      pinch: function(ev) {
        if (!isManipOn(store, 'zoom')) return false;
        zoomBy(ev);
      },

      scroll: function(ev) {
        if (!isManipOn(store, 'zoom')) return false;
        zoomBy(ev);
      }
    };
  },

  nodeRotate: (store, {navigator}) => {
    return {
      drag: function(ev){
        if(ev.which!==1 || !isManip(store, 'nodeRotate')) return false;
        return {
          momentum:false,
          handle:function(ev){
            navigator.rotateNode({x:ev.deltaX, y:ev.deltaY});
          }
        };
      }
    };
  },

  nodeMove: (store, {navigator}) => {
    return {
      drag: function(ev){
        if(ev.which!==1 || !isManip(store,'nodeMove')) return false;
        return {
          momentum:false,
          handle:function(ev){
            navigator.moveNode(ev.deltaX, ev.deltaY);
          }
        };
      }
    };
  },

  nodeScale: (store, {navigator}) => {
    return {
      drag: function(ev){
        if(ev.which!==1 || !isManip(store,'nodeScale')) return false;
        return {
          momentum:false,
          handle: function(ev){
            navigator.scaleNode((ev.deltaX / 30) + (-ev.deltaY / 30))
          }
        };
      }
    };
  }

  //test: (store) => {
  //  return {
  //    click: function(ev) {
  //      console.log('click', ev);
  //    },
  //    dblclick: function(ev) {
  //      console.log('double click', ev);
  //    },
  //    hover: function(ev) {
  //      console.log('hover', ev);
  //    },
  //    dragenter: function(ev) {
  //      console.log('dragenter', ev);
  //    },
  //    dragover: function(ev) {
  //      console.log('dragover', ev);
  //    },
  //    dragleave: function(ev) {
  //      console.log('dragleave', ev);
  //    },
  //    drop: function(ev) {
  //      console.log('drop', ev);
  //    },
  //    mouseup: function(ev) {
  //      console.log('mouseup', ev);
  //    },
  //    mousedown: function(ev) {
  //      console.log('mousedown', ev);
  //    },
  //    hover: function(ev) {
  //      console.log('hover');
  //    }
  //  };
  //}
};

export default tools;



