import THREE from 'three';
import WEBVR from 'webvr';
import { List } from 'immutable';
import { createTranslator } from './translator';
import { convertToPolyMesh, convertToCNSPolyMesh, checkGeometryCache, cacheGeometry } from './bingeom';
import { primitiveToMaterial, getCubeMapTexture, assignTexture } from './materials';
import { polyMeshToBufferGeometry } from './polyMesh';
import { createLight, updateLight } from './light';
import { setTransformOn, setMatrixOn } from './transform';
import initNavigator from './navigator';
import { initManipulators } from './manipulators';

import { getOrFetchBinary, getHash } from '../modules/assets';
import { getScreenPositionNormalAlpha } from './annotation';
import { reportError } from '../modules/sceneIO';
import { resizeCamera, virtualCameraId } from '../modules/sceneGraph';
import { getTools, getCameraPosition, isStereoMode, setPlayerElement, isDeviceTracking  } from '../modules/player';

import getBoundingBox from './utils/getBoundingBox';
import './utils/outlinePass';
import './utils/FXAAShader';

import './utils/vrEffect'; // Extend three with VREffect
import './utils/vrControls'; // extend three with VRControls
import './utils/parallaxBarrierEffect'; // extend three with StereoEffect
import './utils/reticle';
import './utils/viveController';
import './utils/objLoader';

import { ThreeExport } from '../polyMesh';

const BACKGROUND_STYLE_DEFAULT = 0;
const BACKGROUND_STYLE_COLOR = 1;
const BACKGROUND_STYLE_IMAGE = 2;
const BACKGROUND_STYLE_CUBEMAP = 3;
const BACKGROUND_STYLE_ENVIRONMENT = 4;

function initTranslator(store, context, translator) {
  const scene = new THREE.Scene();
  const camera = new THREE.PerspectiveCamera(75, 4 / 3, 0.1, 1000);

  const rendererAttrs = {
    antialias: false,
    precision: 'highp',
    alpha: true,
    preserveDrawingBuffer: true,
  };
  if (context) {
    rendererAttrs.canvas = { style: {} };
    rendererAttrs.context = context;
  }
  let isIOS = false;
  const renderer = new THREE.WebGLRenderer(rendererAttrs);
  renderer.setClearColor( 0xffffff );
  renderer.autoResizeTextures = false;
  renderer.gammaInput = true;
  renderer.gammaOutput = true;
  renderer.toneMapping = THREE.ReinhardToneMapping;
  renderer.physicallyCorrectLights = true;
  if (__BROWSER__) {
    const pixelRatio = 1; //window.devicePixelRatio || 1;
    renderer.setPixelRatio( pixelRatio );
    isIOS = ( window.navigator ) ? ( /iPad|iPhone|iPod/.test(window.navigator.userAgent) && !window.MSStream ) : false;
  }


  let el = null;
  let playerEl = null; // This is the parent element that we control (div.claraplayer)

  const nodes = {};
  const annotations = {};
  const rect = new THREE.Vector2(400, 300);
  let navigator = null;
  let tools = [];
  let interactionHandler = null;
  let cameraId = virtualCameraId;
  let boundingBox = null;
  let prefetched = null;
  let reflectiveFloorMaterial = null;
  let reflectiveFloorMesh = null;

  let noExternalDisplay = true;
  let controller1, controller2;
  let vrElements = new THREE.Object3D();
  let vrDisplay = null;
  let vrReticle = null;
  let vrFloor = null;
  let vrTexture = null;
  let viveControllerButtonPressed = false;
  let currentRenderingMode = 'normal';

  const translatorState = {};

  function getContext() {
    return renderer.getContext();
  }

  // use msaa on the server
  const serverRenderingMode = !!context;

  const composer = new THREE.EffectComposer( renderer );

  const clearPass = new THREE.ClearPass();
  clearPass.enabled = true;
  clearPass.renderToScreen = true;
  composer.addPass( clearPass );

  const texturePass = new THREE.TexturePass();
  texturePass.clear = false;
  texturePass.enabled = false;
  texturePass.renderToScreen = true;
  composer.addPass( texturePass );

  const cubeTexturePass = new THREE.CubeTexturePass( scene, camera );
  cubeTexturePass.clear = false;
  cubeTexturePass.enabled = false;
  cubeTexturePass.renderToScreen = true;
  composer.addPass( cubeTexturePass );

  const msaaRenderPass = new THREE.ManualMSAARenderPass( scene, camera );
  msaaRenderPass.clear = false;
  msaaRenderPass.renderToScreen = true;
  msaaRenderPass.sampleLevel = serverRenderingMode ? 3 : 1;
  msaaRenderPass.unbiased = true;
  msaaRenderPass.enabled = true;
  composer.addPass( msaaRenderPass );

  const renderPass = new THREE.RenderPass( scene, camera, null, 0xffffff );
  //renderPass.clear = true;
  renderPass.renderToScreen = true;
  //renderPass.enabled = ! msaaRenderPass.enabled;
  composer.addPass( renderPass );

  var outlinePass = new THREE.OutlinePass( new THREE.Vector2(window.innerWidth, window.innerHeight), scene, camera);
  outlinePass.edgeStrength = 10;
  outlinePass.edgeGlow = 0;
  outlinePass.edgeThickness = 1;
  outlinePass.pulsePeriod = 0;
  outlinePass.visibleEdgeColor = new THREE.Color(0, 1, 0);
  outlinePass.hiddenEdgeColor = new THREE.Color(0, 1, 0);
  outlinePass.renderToScreen = true;
  composer.addPass( outlinePass );

/*  var effectFXAA = new THREE.ShaderPass(THREE.FXAAShader);
  effectFXAA.uniforms['resolution'].value.set(1 / window.innerWidth, 1 / window.innerHeight );
  effectFXAA.renderToScreen = true;
  composer.addPass( effectFXAA );*/

  const saoPass = new THREE.SAOPass( scene, camera );
  saoPass.downSamplingRatio = serverRenderingMode ? 1 : 1; // fast enough if not full screen (I think.)
  saoPass.intensity = 3;
  saoPass.occlusionSphereWorldRadius = 2;
  saoPass.renderToScreen = true;
  saoPass.blurEnabled = true;
  saoPass.outputOverride = null//"sao";
  saoPass.manualCompositing = false;
  saoPass.blurKernelSize = ((saoPass.downSamplingrendererRatio === 1) ? 6 : 6 ) + ( serverRenderingMode ? 2 : 0 );
  saoPass.enabled = true;
  composer.addPass( saoPass );

  //---For VR Render

  const stereoEffect = new THREE.ParallaxBarrierEffect( renderer );
  const vrControls = new THREE.VRControls( camera );
  const vrEffect = new THREE.VREffect( renderer );

  function resize(width, height) {
    if (el) {
      const elRect = el.parentNode.getBoundingClientRect();
      width = elRect.width;
      height = elRect.height;
    }

    if (width < 10 || height < 10) return;

    rect.set(width, height);

    if (navigator) navigator.resize(rect);
    renderer.setSize(rect.width, rect.height);

    const pixelRatio = renderer.getPixelRatio();
    composer.setSize(rect.width * pixelRatio, rect.height * pixelRatio);
    if (interactionHandler) interactionHandler.resize(rect);

    if (vrEffect !== undefined) vrEffect.setSize(width, height);

    if(height && height!==0) {
      camera.aspect = width/height;
      camera.updateProjectionMatrix();
    }

    //effectFXAA.uniforms['resolution'].value.set(1 / width, 1 / height );
  }

  /*
   * Attach the renderer's canvas element to the DOM?.
   */
  function attach(element) {
    el = element;
    el.insertBefore(renderer.domElement, el.firstChild);

    playerEl = el.parentElement;
    store.dispatch(setPlayerElement(playerEl, renderer.domElement));
    resize();
  }
  function enterVR() {
    vrControls.resetPose(); // zero orientation controls
    vrEffect.requestPresent();
    function texture() {
      // Materials
      var mat = new THREE.LineBasicMaterial( {color: 0xffffff, linewidth: 10} );

      let lineGeometry = new THREE.BufferGeometry();
      let positions = new Float32Array( 80 * 2 * 3 );

      for(let x = -10, i = 0; x < 10; x+=0.5, i++) {
        positions[i*6+0] = x;
        positions[i*6+1] = 0;
        positions[i*6+2] = -10;
        positions[i*6+3+0] = x;
        positions[i*6+3+1] = 0;
        positions[i*6+3+2] = 10;
      }
      for(let z = -10, i = 40; z < 10; z+=0.5, i++) {
        positions[i*6+0] = -10;
        positions[i*6+1] = 0;
        positions[i*6+2] = z;
        positions[i*6+3+0] = 10;
        positions[i*6+3+1] = 0;
        positions[i*6+3+2] = z;
      }

      lineGeometry.addAttribute( 'position', new THREE.BufferAttribute( positions, 3 ) );

      let mesh = new THREE.LineSegments( lineGeometry, mat );
      mesh.mode = THREE.LineSegments;

      return mesh;
    };
    if(vrElements.children.length == 0) {
      var cbgeometry = new THREE.PlaneGeometry( 20, 20, 1, 1 );
      var mat = new THREE.MeshBasicMaterial( {color: 0x666666 } );
      vrFloor = new THREE.Mesh( cbgeometry, mat);
      vrFloor.rotation.x = -Math.PI * 0.5;
      vrTexture = texture();

      vrReticle = new THREE.Reticle(camera);

      vrReticle.addCollider(vrFloor);

      scene.add(vrElements);

      vrElements.add(vrReticle.armObject);
      vrElements.add(vrFloor);
      vrElements.add(vrTexture);
    }

    window.navigator.getVRDisplays().then(function(displays) {
      if (displays.length > 0) {
        vrDisplay = displays[0];
        if(!vrDisplay.capabilities.hasExternalDisplay) {
          scene.add(camera);
          window.addEventListener("touchend", navHandle);
        }
        else {
          noExternalDisplay = false;
          vrControls.standing = true;
          if(vrElements.children.controller1 === undefined) {
            //------Add vive controllers-----------
            controller1 = new THREE.ViveController( 0 );
            controller1.standingMatrix = vrControls.getStandingMatrix();
            vrElements.add( controller1 );

            controller2 = new THREE.ViveController( 1 );
            controller2.standingMatrix = vrControls.getStandingMatrix();
            vrElements.add( controller2 );

            var loader = new THREE.OBJLoader();
            loader.setPath( 'models/' );
            loader.load( 'vr_controller_vive_1_5.obj', function ( object ) {

              var loader = new THREE.TextureLoader();
              loader.setPath( 'models/' );

              var controller = object.children[ 0 ];
              var material = new THREE.MeshBasicMaterial();
              material.map = loader.load( 'onepointfive_texture.png' );
              material.specularMap = loader.load( 'onepointfive_spec.png' );
              controller.material = material;

              controller1.add( object.clone() );
              controller2.add( object.clone() );

            });
          }
          window.addEventListener("thumbpadup", viveNavHandle);
        }
      }
    });


  }

  function exitVR() {
    vrEffect.exitPresent();
    if(noExternalDisplay) {
      scene.remove(camera);
      window.removeEventListener("touchend", navHandle);
    }
    else {
      controller1 = null;
      controller2 = null;
      scene.position.set(0, 0, 0);
      vrElements.position.set(0, 0, 0);
      window.removeEventListener("thumbpadup", viveNavHandle);
    }
    vrControls.standing = false;
    //vrReticle.removeCollider(vrFloor);
    renderer.setScissorTest(false);
    //vrElements.children = [];
    scene.remove(vrElements);
  }

  function viveNavHandle() {
    if(vrReticle.armObject !== null) {
      var pos = vrReticle.getReticlePosition().clone();
      var vrdpPos = camera.position.clone();
      var trans = vrdpPos.sub(pos);
      scene.translateX(trans.x);
      scene.translateZ(trans.z);
      vrElements.translateX(-trans.x);
      vrElements.translateZ(-trans.z);
    }
  }

  function navHandle() {
    if(vrReticle.armObject !== null) {
      var pos = vrReticle.getReticlePosition().clone();
      pos.y = 1.6;
      navigator.changeCameraPosition(pos);
    }
  }

  function orientHandle() {
    store.dispatch({type:'POKE'});
  }

  function deviceTrackingTransition(enabled) {
    window[enabled ? 'addEventListener' : 'removeEventListener']('deviceorientation', orientHandle);
  }

  function handleStateChanges() {
    // TODO: only do this if the player has changed. Requires moving the cameraWorldMatrix
    // out of the player state.
    //

    function stateChange(key, handler) {
      const newState = store.getIn(['player', key]);
      if (translatorState[key] !== newState) {
        handler(newState, translatorState[key]);
        translatorState[key] = newState;
      }
    }
    stateChange('deviceTracking', deviceTrackingTransition);
  }

  /*
   * After all nodes have been translated.
   */
  let renderDisplaySceneTimeout = null;
  let lastRenderStartTime = -1;
  let lastRenderDeltaTime = -1;

  function updateRenderTime() {

    const currentRenderStartTime = Date.now();
    if( lastRenderStartTime > 0 ) {
      lastRenderDeltaTime = currentRenderStartTime - lastRenderStartTime;
    } else {
      lastRenderDeltaTime = -1;
    }
    lastRenderStartTime = currentRenderStartTime;

  }

  function postTranslate(renderingMode, loaded) {

    if( renderDisplaySceneTimeout ) {
      clearTimeout( renderDisplaySceneTimeout );
    }

    updateRenderTime();

    if (loaded) {
      handleStateChanges();

      if( reflectiveFloorMesh && reflectiveFloorMesh.data.get('reflectiveFloor') ) {
        reflectiveFloorMaterial.distanceFade = reflectiveFloorMesh.data.get('reflectiveFloorFade') * 0.0001;
        reflectiveFloorMaterial.roughness = reflectiveFloorMesh.data.get('reflectiveFloorRoughness');
        reflectiveFloorMaterial.metalness = reflectiveFloorMesh.data.get('reflectiveFloorMetallic');
        reflectiveFloorMaterial.opacity = reflectiveFloorMesh.data.get('reflectiveFloorOpacity');
        reflectiveFloorMaterial.fresnelStrength = reflectiveFloorMesh.data.get('reflectiveFloorFresnel');
        reflectiveFloorMaterial.reflectivity = 0.5;
        reflectiveFloorMaterial.render( renderer, scene, camera );

        //reflectiveFloorMesh.material = new THREE.MeshStandardMaterial( { emissive: 0xff00ff, emissiveMap: reflectiveFloorMaterial.material.uniforms.tReflection.value } );
      }
      //renderer.setClearColor( renderPass.clearColor, renderPass.clearAlpha );


      if (isDeviceTracking(store)) vrControls.update();

      cubeTexturePass.camera = camera;

      if (renderingMode === 'normal') {
        //renderer.autoClear = false;
        renderer.setClearColor( 0xffffff );
        renderPass.enabled = false;
        //effectFXAA.enabled = false;
        msaaRenderPass.enabled = true;
        //renderer.setClearAlpha( 0.9 );
        composer.render();
      } else if (renderingMode === 'vr') {
        renderPass.enabled = true;
        //effectFXAA.enabled = true;
        msaaRenderPass.enabled = false;
        vrDisplay = vrEffect.getVRDisplay();
        if(!noExternalDisplay) {
          controller1.update();
          controller2.update();
        }
        vrReticle.update();
        vrEffect.render(scene, camera, null, true);
      } else if (renderingMode === 'lenticular') {
        stereoEffect.render(scene, camera);
      }

      if (!serverRenderingMode || lastRenderDeltaTime > 0 ) {
        renderDisplaySceneTimeout = setTimeout( function() {

          updateRenderTime();

          msaaRenderPass.sampleLevel = 2;
          composer.render();
          msaaRenderPass.sampleLevel = 1;

          renderDisplaySceneTimeout = setTimeout( function() {

            updateRenderTime();

            msaaRenderPass.sampleLevel = 3;
            composer.render();
            msaaRenderPass.sampleLevel = 1;
            lastRenderStartTime = -1;
          }, Math.max( lastRenderDeltaTime, 100 ) * 4 );
        }, Math.max( lastRenderDeltaTime, 100 ) * 4 );
      }
    }
  }

  function getReticle() {
    return vrReticle;
  }
  function getVRDisplay() {
    return vrDisplay;
  }
  /*
   * View attributes have changed (rect, camera data, etc).
   */
  function updateView(player) {
    if (interactionHandler && tools !== getTools(store)) {
      tools = getTools(store);
      interactionHandler.updateTools(tools, { navigator });
    }
    setMatrixOn(camera, getCameraPosition(store));
  }

  /*
   * Scene is loaded.
   */
  function onLoad() {
    boundingBox = getBoundingBox(scene);
    navigator = initNavigator(store, boundingBox, rect, cameraId);
    store.setNavigator(navigator);

    if (!context) {
      interactionHandler = initManipulators(store, renderer.domElement);
      interactionHandler.resize(rect);
    }
  }

  function getAnnotations() {
    return annotations;
  }

  function getNavigator() {
    return navigator;
  }

  function addToParent(id, node, obj, skipReplace) {
    // console.log('Add toParent: ', node && node.get('parent'), obj);
    const parentId = node && node.get('parent');
    const parent = (parentId && nodes[parentId]) || scene;
    obj.name = node.get('name'); // eslint-disable-line no-param-reassign
    obj.nodeId = id;

    if (nodes[id]) {
      if (skipReplace) return;

      const children = [].concat(nodes[id].children);
      for (let i=0; i<children.length; i++) {
        nodes[id].remove(children[i]);
        obj.add(children[i]);
      }
      parent.remove(nodes[id]);
      if(nodes[id].geometry) nodes[id].geometry.dispose();
    }

    nodes[id] = obj;
    parent.add(nodes[id]);
  }

  function shouldPrefetch(properties) {
    return prefetched && properties && properties.get('prefetch');
  }

  const plugs = {
    PolyMesh: (id, data, prevData, node, prevNode, properties) => {
      const visible = properties.get('visible');
      const mesh = data.get('mesh');
      //console.log(mesh, prevData && prevData.get('mesh'));

      //const collapseMaps = true;
      const indexingMode = renderer.extensions.get( 'OES_element_index_uint' ) ? ThreeExport.CompactIndices : ThreeExport.NoIndices;
      const interleaved = false;
      const oldMesh = nodes[id];
      const prevTriangulation = oldMesh && oldMesh.userData.triangulation;
      const prevIndexing = oldMesh && oldMesh.userData.renderIndexing;
      const prevValueMap = oldMesh && oldMesh.userData.valueMap;
      const bufferInfo = mesh && ThreeExport.exportBufferGeometry( mesh, indexingMode, interleaved, null, prevData, prevTriangulation, prevIndexing, prevValueMap);

      let geometry, triangulation, triangulationChanged, renderIndexing, valueMap;
      if(bufferInfo){
        geometry = bufferInfo.geometry;
        triangulation = bufferInfo.triangulation;
        triangulationChanged = bufferInfo.triangulationChanged;
        renderIndexing  = bufferInfo.renderIndexing;
        valueMap = bufferInfo.valueMap;
      }

      let obj;

      if (triangulationChanged || !oldMesh){
        obj = geometry && new THREE.Mesh(geometry, primitiveToMaterial(store));
      } else {
        obj = oldMesh;
        if(geometry) {
          obj.geometry.dispose();
          obj.geometry = geometry;
        }
        //obj.material.dispose();
        //obj.material = primitiveToMaterial(store);
      }

      if (obj) {
        obj.visible = visible;
        obj.userData.triangulation = triangulation;
        obj.userData.renderIndexing = renderIndexing;
        obj.userData.valueMap = valueMap;
      }

      addToParent(id, node, obj || new THREE.Object3D());
    },

    BinMesh: (id, data, prevData, node, prevNode, properties) => {
      const visible = properties.get('visible');
      const mesh = data.get('mesh');

      //const collapseMaps = true;
      //const polyMesh = CNS.ThreeImport.importBufferGeometry(geometry, collapseMaps);
      const indexingMode = renderer.extensions.get( 'OES_element_index_uint' ) ? ThreeExport.CompactIndices : ThreeExport.NoIndices;
      const interleaved = false;
      const bufferInfo = mesh && ThreeExport.exportBufferGeometry( mesh, indexingMode, interleaved );
      const obj = bufferInfo && bufferInfo.geometry && new THREE.Mesh(bufferInfo.geometry, primitiveToMaterial(store));
      if (obj) obj.visible = visible;
      addToParent(id, node, obj || new THREE.Object3D());
    },

    Light: (id, data, prevData, node) => {
      let obj = nodes[id];
      if (!obj) {
        obj = createLight(data);
        if (obj) addToParent(id, node, obj);
      } else {
        updateLight(data, obj);
      }
    },

    Bone: (id, data, prevData, node) => {
      // FIXME: Make bones work, this just adds a null object
      if (!nodes[id]) addToParent(id, node, new THREE.Object3D());
    },


    Null: (id, data, prevData, node) => {
      if (!nodes[id]) addToParent(id, node, new THREE.Object3D());
    },

    Transform: (id, data) => {
      if (!nodes[id] || !data || !data.get('transform')) return;
      setMatrixOn(nodes[id], data.get('transform'));
    },

    Material: (id, data) => {
      if (!nodes[id]) return;
      nodes[id].material = primitiveToMaterial(store, renderer, data, nodes[id].material);
    },

    Environment: (id, data) => {
      //console.log(data.toJS());
      const backgroundStyle = data.get('backgroundStyle');
      clearPass.enabled = false;
      cubeTexturePass.enabled = false;

      switch( backgroundStyle ) {

        case BACKGROUND_STYLE_COLOR:
          clearPass.enabled = true;
          clearPass.clearColor = data.get('backgroundColor');
          clearPass.clearAlpha = data.get('backgroundOpacity');
          break;
        case BACKGROUND_STYLE_IMAGE:
          texturePass.enabled = true;
          texturePass.map = assignTexture(store, renderer, data.get('background'));
          break;
        case BACKGROUND_STYLE_CUBEMAP:
        case BACKGROUND_STYLE_ENVIRONMENT: {

            clearPass.enabled = true;
            clearPass.clearColor = 0x000000;
            clearPass.clearAlpha = 0.0;

            cubeTexturePass.enabled = true;

            let cubeMapNode;
            let cubeMapBlurring;

            if( backgroundStyle === BACKGROUND_STYLE_ENVIRONMENT ) {
              cubeMapNode = data.get('environmentMap');
              if( cubeMapNode ) {
                cubeMapNode = cubeMapNode.get('specularCubeMap');
              }
              cubeMapBlurring = data.get('environmentMapBlurring' );
            }
            else {
              cubeMapNode = data.get('cubeMap');
              cubeMapBlurring = data.get('cubeMapBlurring');
            }

            if( cubeMapNode ) {
              cubeTexturePass.envMap = getCubeMapTexture(store, renderer, cubeMapNode, true );
              cubeTexturePass.envMapIntensity = 1.0;
              cubeTexturePass.opacity = data.get('backgroundOpacity');
              cubeTexturePass.roughness = data.get('cubeMapBlurring');
            }
            else {
              cubeTexturePass.enabled = false;
            }
          }

          break;

        default:
          clearPass.enabled = true;
          clearPass.clearColor = new THREE.Color(1,1,1);
          clearPass.clearAlpha = 0;
          break;
      }
    },

    Player: (id, data) => {
      const newCameraId = data.getIn(['camera', 'id']);
      if (newCameraId && newCameraId !== cameraId) {
        cameraId = newCameraId;
        if (navigator) navigator.switchCamera(cameraId);
      }

      const styleToToneMapping = {
        Linear: THREE.LinearToneMapping,
        Reinhard: THREE.ReinhardToneMapping,
        Cineon: THREE.CineonToneMapping,
        Filmic: THREE.Uncharted2ToneMapping,
      };

      renderer.toneMapping = styleToToneMapping[data.get('toneMapStyle')];
      renderer.toneMappingExposure = data.get('toneMapExposureGain');
      renderer.toneMappingWhitePoint = data.get('toneMapWhitePoint');
      renderer.gammaInput = true;
      renderer.gammaOutput = true;
      renderer.autoClear = false;

      saoPass.enabled = data.get('saoPass');
      saoPass.intensity = data.get('saoIntensity');
      saoPass.occlusionSphereWorldRadius = data.get('saoScale');

      if( data.get('reflectiveFloor') && ( ( ! data.get('reflectiveFloorExcludeServer') ) || __BROWSER__ ) ) {

        if( ! reflectiveFloorMaterial ) {
          const mirrorGeo = new THREE.PlaneBufferGeometry( 1, 1 );
          reflectiveFloorMaterial = new THREE.GlossyMirror( { clipBias: 0.003, textureWidth: 1024, textureHeight: 1024, color: 0x777777, localMirrorNormal: new THREE.Vector3( 0, 1, 0 ) } );
          reflectiveFloorMesh = new THREE.Mesh( mirrorGeo, reflectiveFloorMaterial.material );
        }

        reflectiveFloorMaterial.specularColor = new THREE.Color( 0xffffff );
        reflectiveFloorMaterial.specularColor = new THREE.Color( 0xffffff );

        reflectiveFloorMesh.scale.x = data.get('reflectiveFloorWidth');
        reflectiveFloorMesh.scale.y = data.get('reflectiveFloorWidth');
        reflectiveFloorMesh.rotation.x = -Math.PI * 0.5;
        reflectiveFloorMesh.position.y = data.get('reflectiveFloorHeight');

        reflectiveFloorMesh.data = data;

        if( ! reflectiveFloorMesh.parent ) {
          scene.add( reflectiveFloorMesh );
        }
      }
      else {
        if( reflectiveFloorMesh && reflectiveFloorMesh.parent ) {
          scene.remove( reflectiveFloorMesh );
        }
      }
    },

    Camera: (id, data) => {
      camera.fov = data.get('fieldOfView');
      camera.aspect = data.get('aspectRatio');
      camera.near = data.get('nearClip');
      camera.far = data.get('farClip');
      camera.updateProjectionMatrix();
    },
  };

  const translators = {
    Scene: (id, data, prevData, node, prevNode) => {
      for (const plug of ['Player','Environment']) {
        plugs[plug](id, data.get(plug), prevData && prevData.get(plug), node, prevNode);
      }
    },

    PolyMesh: (id, data, prevData, node, prevNode) => {
      const visible = data.getIn(['Properties', 'visible']);
      const prevVisible = prevData && prevData.getIn(['Properties', 'visible']);

      const meshChanged = !prevData || prevData.get('PolyMesh') !== data.get('PolyMesh')
          || visible !== prevVisible || (!visible && shouldPrefetch(data.get('Properties')));

      for (const plug of ['PolyMesh', 'Transform', 'Material']) {
        if (!prevData || meshChanged || prevData.get(plug) !== data.get(plug)) {
          if (plug === 'PolyMesh' || visible) {
            plugs[plug](id, data.get(plug), prevData && prevData.get(plug),
                node, prevNode, data.get('Properties'));
          }
        }
      }
    },

    BinMesh: (id, data, prevData, node, prevNode) => {
      const visible = data.getIn(['Properties', 'visible']);
      const prevVisible = prevData && prevData.getIn(['Properties', 'visible']);

      const meshChanged = !prevData || prevData.get('BinMesh') !== data.get('BinMesh')
          || visible !== prevVisible || (!visible && shouldPrefetch(data.get('Properties')));
      for (const plug of ['BinMesh', 'Transform', 'Material']) {
        if (!prevData || meshChanged || prevData.get(plug) !== data.get(plug)) {
          if (plug === 'BinMesh' || visible) {
            plugs[plug](id, data.get(plug), prevData && prevData.get(plug),
                node, prevNode, data.get('Properties'));
          }
        }
      }
    },

    Light: (id, data, prevData, node, prevNode) => {
      for (const plug of ['Light', 'Transform']) {
        plugs[plug](id, data.get(plug), prevData && prevData.get(plug), node, prevNode);
      }
    },

    Null: (id, data, prevData, node, prevNode) => {
      for (const plug of ['Null', 'Transform']) {
        plugs[plug](id, data.get(plug), prevData && prevData.get(plug), node, prevNode);
      }
    },

    Model: (id, data, prevData, node, prevNode) => {
      for (const plug of ['Null', 'Transform']) {
        plugs[plug](id, data.get(plug), prevData && prevData.get(plug), node, prevNode);
      }
    },

    Camera: (id, data, prevData, node, prevNode) => {
      if (cameraId === id) {
        plugs.Camera(id, data.get('Camera'), // eslint-disable-line new-cap
            prevData && prevData.get('Camera'), node, prevNode);
      }
    },

    ErrorNode: (id, data, prevData, node) => {
      addToParent(id, node, new THREE.Object3D());
    },

    Annotation: (id, data) => {
      const calcDirection = data.getIn(['Annotation','directionMode']) === 'awayFromOrigin';

      if (data.getIn(['Properties', 'visible'])) {

        annotations[id] = {
          id: id,
          annotation: data.get('Annotation').toJS(),
          position: getScreenPositionNormalAlpha(id, calcDirection, data.getIn(['Annotation', 'directionTarget'])),
        };
      }
    },

    Material: (id, data, prevData, node, prevNode) => {
      if (!shouldPrefetch(data.get('Properties'))) return;

      for (const plug of ['Null', 'Material']) {
        plugs[plug](id, data.get(plug), prevData && prevData.get(plug), node, prevNode);
      }
    },

    Image: (id, data, prevData, node) => {
      if (!shouldPrefetch(data.get('Properties'))) return;
      const img = getTextureImage(store, data.get('Image'));
    },

    Bone: (id, data, prevData, node, prevNode) => {
      for (const plug of ['Bone', 'Transform']) {
        plugs[plug](id, data.get(plug), prevData && prevData.get(plug), node, prevNode);
      }
    },
  };

  for (const UnhandledType of ['Objects', 'MaterialLibrary']) {
    translators[UnhandledType] = (id, data, prevData, node) => {
      addToParent(id, node, new THREE.Object3D(), true);
    };
  }
  for (const UnhandledType of ['Renderers', 'Passes', 'Pass', 'Renderer']) {
    translators[UnhandledType] = (id, data, prevData, node) => {};
  }

  // If necessary, return a function that determines whether a node is dirty or not.
  // used for flagging nodes as dirty when we want to 'prefetch' them, after scene
  // has been fully loaded.
  // mutates 'state'.
  function dirtyCheck(loaded) {
    if (prefetched || !loaded) return false;

    prefetched = true;
    return (s, id, nextResult) => {
      const props = nextResult.get('Properties');
      return props && shouldPrefetch(props);
    };
  }

  /*
   * Translate a node, either create, update or delete
   */
  function translate(id, evalNode, prevEvalNode, node, prevNode) {
    if (!node && prevNode) {
       let id = prevNode.get('id');
       const k= nodes[id];
       delete nodes[id];
       k.parent.remove(k);
      // TODO: delete
      return;
    }

    const type = node.get('type');
    if (translators[type]) {
      // console.log('translate', type, node.get('name'));
      translators[type](id, evalNode, prevEvalNode, node, prevNode);
    } else {
      console.warn('translate SKIP', type, node.get('name')); // eslint-disable-line no-console
    }
  }

  function raycastSelect(mousePosition){
    let hits = [];
    let selectedObjects = [];
    const ray = new THREE.Raycaster()
    ray.setFromCamera(mousePosition, camera);
    let objectsRoot = scene;

    var gizmo = objectsRoot.getObjectByName('GizmoV2');
    
    gizmo.traverse((node)=>{
      if (!node.visible) return;
      if (!node.geometry) return;

      if (node.geometry.boundingBox === null){
        node.geometry.computeBoundingBox();
      }

      const geometry = node.geometry;

      if (geometry.boundingBox.isEmpty() === false) {
        node.matrix.compose(node.position, node.quaternion, node.scale);
        node.updateMatrixWorld(true);
        const inter = ray.intersectObject(node);
        if (inter.length>0) {
          hits.push({id: node.name, node: node, data: inter});
        }
      }
    });


    scene.children.forEach((child)=>{
      if(child.type==="Object3D" && child.name==="Objects") objectsRoot=child;
    });
    objectsRoot.traverse((node)=>{
      if (!node.visible) return;
      if (!node.geometry) return;

      if (node.geometry.boundingBox === null){
        node.geometry.computeBoundingBox();
      }

      const geometry = node.geometry;

      if (geometry.boundingBox.isEmpty() === false) {
        node.matrix.compose(node.position, node.quaternion, node.scale);
        node.updateMatrixWorld(true);
        const inter = ray.intersectObject(node);
        
        if (inter.length>0) {
          hits.push({id: node.nodeId, node: node, data: inter});
        }
      }
    });
    hits.sort((a,b)=>{
      return a.data[0].distance-b.data[0].distance;
    });

    return hits;
  }

  function highlightSelection(rootNodeId) {
    outlinePass.selectedObjects = [];
    if(rootNodeId !== null) {
      var rootNode = nodes[rootNodeId];
      if(rootNode !== undefined) {
        rootNode.traverse((node)=>{
          if(node.name !== "optSymbols" && node.parent.name !== "optSymbols" && node.type === "Mesh") {
            outlinePass.selectedObjects.push(node);
          }
        });
      }
    }
  }


  function toScreenPosition(rootNodeId) {
    if(rootNodeId) {
      var obj = nodes[rootNodeId];
      if(obj) {
        var vector = new THREE.Vector3();

        var widthHalf = 0.5*renderer.context.canvas.width;
        var heightHalf = 0.5*renderer.context.canvas.height;

        obj.updateMatrixWorld();
        var bbox = getBoundingBox(obj);
        var height = bbox.max.y - bbox.min.y;
        obj.updateMatrixWorld();
        vector.setFromMatrixPosition(obj.matrixWorld);
        vector.y += height;
        vector.project(camera);

        vector.x = ( vector.x * widthHalf ) + widthHalf;
        vector.y = - ( vector.y * heightHalf ) + heightHalf;

        return {
          x: vector.x,
          y: vector.y
        };
      }
   }
  };

  function createGizmo() {
    var gizmo = new THREE.Object3D();
    gizmo.name = "GizmoV2";

    var material = new THREE.MeshBasicMaterial( { color: 0x00ff00 } );
    material.side = THREE.DoubleSide;
    //Draw rotate circle geometry
    var coneGeometry1 = new THREE.ConeGeometry( 0.2, 0.6, 25 );
    var rotGeometry = new THREE.TorusGeometry( 1.5, 0.05, 16, 100, 2.5);
    coneGeometry1.rotateX(Math.PI);
    coneGeometry1.translate(1.45, 0, 0);
    rotGeometry.merge(coneGeometry1);
    var clone1 = rotGeometry.clone();
    clone1.rotateZ(Math.PI);
    rotGeometry.merge(clone1); 
    var rotateSyb = new THREE.Mesh( rotGeometry, material );
    rotateSyb.name = 'rotateSymbol';
    rotateSyb.visible = true;
    //Draw translate cross geometry
    var coneGeometry2 = new THREE.ConeGeometry( 0.2, 0.6, 25 );
    var transGeometry = new THREE.CylinderGeometry( 0.05, 0.05, 3, 32 );
    var cloneCone = coneGeometry2.clone();
    coneGeometry2.translate(0, 1.5, 0);
    cloneCone.rotateX(Math.PI);
    cloneCone.translate(0,-1.5,0);
    transGeometry.merge(coneGeometry2);
    transGeometry.merge(cloneCone);
    var clone2 = transGeometry.clone();
    clone2.rotateZ(Math.PI/2);
    transGeometry.merge(clone2);
    material.side = THREE.DoubleSide;
    var translateSyb = new THREE.Mesh( transGeometry, material );
    translateSyb.name = 'translateSymbol';
    translateSyb.visible = true;

    gizmo.add(translateSyb);
    gizmo.add(rotateSyb);
    gizmo.position.set(0, 0.1, 0);
    gizmo.rotateX(Math.PI / 2);
    gizmo.scale.set(0.4, 0.4, 0.4);
    scene.add(gizmo);
    gizmo.visible = false;
  }

  function updateGizmo(rootNodeId, mode) {
    var gizmo = scene.getObjectByName('GizmoV2');
    
    if(rootNodeId !== null) {
      var rootNode = nodes[rootNodeId];
      if (rootNode !== undefined) {
        var bbox = getBoundingBox(rootNode);
        var length = bbox.max.x - bbox.min.x;
        var width = bbox.max.z - bbox.min.z;
        var dis = length > width ? length : width;
        gizmo.scale.set(dis/2, dis/2, dis/2);

        gizmo.parent = rootNode;
        gizmo.visible = true;
        var tanSyb = gizmo.getObjectByName("translateSymbol");
        var rotSyb = gizmo.getObjectByName("rotateSymbol");
        if(mode === "Rotate Item") {
          tanSyb.visible = false;
          rotSyb.visible = true;
        }
        if(mode === "Translate Item") {
          rotSyb.visible = false;
          tanSyb.visible = true;
        }
        if(mode === "orbit"){
          tanSyb.visible = false;
          rotSyb.visible = false;
        }

      }
    }
    else {
      gizmo.visible = false;
    }

  }

  return {
    attach,
    resize,
    translate,
    raycastSelect,
    createGizmo,
    updateGizmo,
    enterVR,
    exitVR,
    highlightSelection,
    toScreenPosition,
    getReticle,
    getVRDisplay,
    postTranslate,
    dirtyCheck,
    updateView,
    onLoad,
    getAnnotations,
    getContext,
    getNavigator,
    scene,
    camera,
  };
}


export default function threeTranslator(store, context) {
  return createTranslator(store, initTranslator, context);
}



