import THREE from 'three';
import cuid from 'cuid';
import uuid from 'node-uuid';
import { Map, List, fromJS } from 'immutable';
import { forEach, map, mapObjIndexed, curry, pick, reduce } from 'ramda';
import createReducer from './utils/createReducer';
import { addFile } from './assets';
//import { fetchmaterial } from './files';
import { LOAD_SCENE, ADD_NODE, ADD_VR_CAMERA, SET_VR_CAMERA, DELETE_NODE } from './actionTypes';

import { getWorldTransform } from './scene';

const SET='SET';
const MERGE='MERGE';
const MULTI='MULTI';
const REPARENT='REPARENT';
const ADD_OPERATOR='ADD_OPERATOR';
const virtualCameraId='cccccccc-cccc-1ccc-accc-cccccccccccc'
const vrCameraId='bbbbbbbb-bbbb-0bbb-zbbb-bbbbbbbbbbbb'

/**
 * This module contains actions to manipulate the scene graph.
 *
 * @module sceneGraph
 * @public
 *
 */

/*
 * Reducer
 */
function reducer(records) {

  function createOperator(OperatorRecord, type, props, name) {
    if (!name) name = type;

    let Op = new OperatorRecord({type, name});
    // Calling merge directly so that the immutable shim has its `set` method called
    // Filtering the operator to only known keys.
    return Op.merge(pick(OperatorRecord.keys(), props));
  };

  function buildNode({id,name,type,children,parent,plugs}) {
    children = children ? List(children) : List();
    let warnings = List();

    const initPlugs = Map(mapObjIndexed((ops, plug) => {
      if (!records[plug]) {
        warnings = warnings.push(`No Plug: ${plug}`);
        return List();
      }
      return List(ops.map((op) => {
        if (!Array.isArray(op)) throw new Error('Expect op to be an array of 2 or 3');

        let [type,props,name] = op;
        let OperatorRecord = records[plug][type];

        if (!OperatorRecord) {
          warnings = warnings.push(`No Operator: ${plug} - ${type}`);
          //console.log('WARNING:', warnings.toJS());
          OperatorRecord = records.None.NoOp;
        }
        return createOperator(OperatorRecord, type, props, name);
      }));
    }, plugs));

    return Map({id, name, type, children, parent, plugs: initPlugs, warnings});
  };

  function errorNode({id, name, children, parent}) {
    children = children ? List(children) : List();
    return Map({id, name, type: 'ErrorNode', children, parent, plugs: Map()});
  };


  function addOperator(state, path, operator) {
    let [id,plug] = path;
    let ops = state.getIn([id,'plugs',plug]);
    if (!ops) throw new Error(`Invalid path to 'addOperator': ${id}:${plug}`);
    state.setIn([id,'plugs',plug], ops.push(buildOperator(plug)(operator)));
  };

  let initialState = Map();

  const sceneReducer = createReducer(initialState, {
    [SET](state, {path,value}) {
      if (path.length && path.length ===2 && path[1] === 'name') {
        return state.setIn(path, value);
      }
      if (!path.length || path.length !== 4) {
        throw new Error('SET requires a path with length 4');
      }
      const [node,plug,idx,key] = path;
      const Op = state.getIn([node,'plugs',plug,idx]);
      if (!Op) throw new Error('Setting data on unknown operator: '+path);
      return state.setIn([node,'plugs',plug,idx,key], value);
    },

    [MERGE](state, {path,attrs}) {
      if (!path.length || path.length !== 3) {
        throw new Error('MERGE requires a path with length 3');
      }
      if (typeof attrs !== 'object') {
        throw new Error('MERGE requires an object to merge in');
      }
      const [node,plug,idx] = path;
      return state.mergeIn([node,'plugs',plug,idx], attrs);
    },

    [ADD_NODE](state, attrs) {
      const {id, parent} = attrs;
      const parentNode = parent && state.get(parent);
      if (parent && !parentNode) throw new Error(`Unknown parent: '${parent}'`);

      const prevNode = state.get(id);
      if (prevNode && prevNode.get('plugs')) throw new Error(`Adding a node that already exists: ${id}`)
      const node = attrs.node || buildNode(attrs);
      state = state[prevNode ? 'merge' : 'set'](id, node);

      if (parent && !state.getIn([parent, 'children']).includes(id)) {
        state = state.setIn([parent,'children'], parentNode.get('children').push(id));
      }
      return state;
    },

    [DELETE_NODE](state, id) {
      let node = state.get(id);
      if (!node) return state;
      let parent = node.get('parent');
      if (parent) {
        let idx = state.getIn([parent,'children']).indexOf(id);
        if (idx !== -1) state = state.setIn([parent, 'children'], state.getIn([parent,'children']).remove(idx))
      }
      return state.delete(id);
    },

    [ADD_OPERATOR](state, attrs) {
      const { id, plug, op } = attrs;
      const node = state.get(id);
      if (!node) throw new Error(`Unknown node: ${id}`);

      const ops = node.getIn([ 'plugs', plug ]);
      if (!ops) throw new Error(`Unknown plug: ${plug}`);

      if (!Array.isArray(op)) throw new Error('Expect op to be an array of 2 or 3');

      const [type,props,name] = op;
      const OperatorRecord = records[plug][type];

      if (!OperatorRecord) throw new Error(`No Operator: ${plug} - ${type}`);

      return state.setIn([id,'plugs',plug], ops.push(createOperator(OperatorRecord, type, props, name)));
    },

    [MULTI](state, ops) {
      let newState = state;
      forEach((op) => {
        if (![SET,MERGE,ADD_NODE,DELETE_NODE,REPARENT].includes(op.type)) {
          throw new Error("Only sceneGraph operations allowed in MULTI: "+op.type);
        }
        newState = sceneReducer(newState, op);
      }, ops);
      return newState;
    },

    [REPARENT](prevState, { parent, childIds }) {
      let state = prevState;

      forEach((childId) => {
        const childParentId = state.getIn([childId,'parent']);
        let idx = state.getIn([childParentId, 'children']).indexOf(childId);
        if (idx !== -1) state = state.setIn([childParentId, 'children'], state.getIn([childParentId,'children']).remove(idx));

        state = state.setIn([parent, 'children'], state.getIn([parent, 'children']).push(childId));
        state = state.setIn([childId, 'parent'], parent);
      }, childIds);
      return state;
    },

    [LOAD_SCENE](state, { scene }) {
      let newState = state.merge(scene);
      newState = newState.get(virtualCameraId) ? newState : newState.set(virtualCameraId, buildNode({
        id: virtualCameraId,
        name: 'CAMERA',
        type: 'Camera',
        plugs: {
          Camera: [['Camera', {}]],
          Transform: [['Transform', {}]],
          Properties: [['Default', {}]],
        },
      }));
      newState = newState.get(vrCameraId) ? newState : newState.set(vrCameraId, buildNode({
        id: vrCameraId,
        name: 'VR CAMERA',
        type: 'Camera',
        plugs: {
          Camera: [['Camera', {fieldOfView:45}]],
          Transform: [['Transform', {translation:{x:0, y:1.6, z:1}}]],
          Properties: [['Default', {}]],
        },
      }));
      return newState;
    },

    [SET_VR_CAMERA](state, cameraId){
      const camNode = state.get(cameraId)
      if(camNode.type === 'Camera') return state.set(vrCameraId, camNode);
      else return state;
    },

  });

  return sceneReducer;
}

/**
 * Add a node to the scene.
 *
 * @public
 * @param {Node} node
 * @returns {Promise}
 */
export function addNode(node)  {
  let {id,name,type,plugs,parent} = node;
  if (!id) id = uuid.v4(); //cuid();
  name = name || id;

  return {
    type: ADD_NODE,
    payload: {id,name,type,plugs,parent},
    resolve: id,
  };
};

export function setVRCamera(camId){
  return { type: SET_VR_CAMERA, payload: camId };
}

/**
 * Deletes a node
 *
 * @public
 * @param {String} id The node id
 * @returns {Promise}
 */
export function deleteNode(id) {
  return {
    type: DELETE_NODE,
    payload: id
  }
};

/**
 *
 * Add an operator to a plug of a node.
 */
export function addOperator(id, plug, op) {
  return {
    type: ADD_OPERATOR,
    payload: { id, plug, op },
  };
};

/**
 * get a property
 *
 * @param {Path} path to the operator property.
 * @returns {any} value
 *
 * @example
 * // scene.get([uuid
 */
export function get(store, path) {
  let p = ['sceneGraph',path[0]];
  if (path.length === 2 && (path[1] === 'name' || path[1] === 'type')) {
    return store.getIn(['sceneGraph',path[0],path[1]]);
  }
  if (path.length >= 2) p = p.concat(['plugs',path[1]]);
  if (path.length >= 3) p = p.concat(path.slice(2));
  return store.getIn(p);
};

export function getVRCamera(store){
  return store.getIn(['sceneGraph', vrCameraId]);
}

/**
 * set a property on an operator. This takes a Path array,
 * and a value
 *
 * @param {Path} path includes the operator property.
 * @param {any} value
 * @returns {Promise}
 *
 */
export function set(path, value) {
  return {type: SET, payload: {path, value}, meta: {remote: true}};
};

/**
 * Merges an object into an operator.
 *
 * @param {Path} path to the operator.
 * @param {Object} attrs
 * @returns {Promise}
 *
 */
export function merge(path, attrs) {
  return {type: MERGE, payload: {path, attrs}};
};

/**
 * An array of scene actions.
 *
 * @param {Action[]} ops
 * @returns {Promise}
 */
export function multi(ops) {
  return {type: MULTI, payload: ops};
};

/**
 * Reparent nodes to the new parent.
 *
 * @public
 * @param {String} parent id of the new parent node
 * @param {Array} childIds list of ids to reparent
 *
 * @example
 * // reparent children of 'MyModel' to 'NewParentNull'
 * const parentId = scene.find({ name: 'NewParentNull' });
 * const nodesToReparent = scene.filter({ from: { name: 'MyModel' } });
 * sceneGraph.reparent(parentId, nodesToReparent);
 */
export function reparent(parent, childIds) {
  return { type: REPARENT, payload: { parent, childIds } };
};

/**
 * Return the parent id of the node.
 */
export function findParent(store, id) {
  return store.getIn(['sceneGraph',id,'parent']);
};

/**
 * Return the root parent id of the node.
 */
export function findRoot(store, id) {
  const parent = store.getIn(['sceneGraph',id,'parent']);
  return parent ? findRoot(store, parent) : id;
};

/**
 * Clone nodes. Given a list of ids, this will copy the node with a new id,
 * and update the parent and child of each node. By passing in a map of *old
 * parent id* to *new parent id*, the nodes can be cloned to a different location
 * in the sceneGraph (or from one to another).
 *
 * @public
 * @param {Array} ids list of ids to clone
 * @param {Object} nodeMap of {oldNodeId: newNodeId} Can be used for passing
 *                         new parents, or new depencies.
 * @param {Object?} options
 * @param {Boolean?} options.cloneDependencies Clone dependencies as well.
 *
 * @example
 * // clone children of 'MyModel' to 'NewParentNull'
 * const parentId = scene.find({ name: 'NewParentNull' });
 * const myModelId = scene.find({ name: 'MyModel' });
 * const nodesToClone = scene.filter({ from: { id: myModelId } });
 * sceneGraph.clone(nodesToClone, { [myModelId]: parentId });
 * // clone dependencies
 * sceneGraph.clone(nodesToClone, { [myModelId]: parentId }, {cloneDependencies: true});
 */
export function clone(ids, nodeMap={}, options={}) {
  const { cloneDependencies } = options;
  const nodes = reduce((acc, id) => { acc[id] = true; return acc; }, {}, ids);
  const deps = {};
  const assets = {};

  return (store) => {
    // Find dependencies
    function checkDependency(id) {
      const name = store.getIn(['sceneGraph', id, 'name']);
      store.getIn(['sceneGraph', id, 'plugs']).forEach((ops, plug) => {
        ops.forEach((op, opIdx) => {
          op.getReferences().forEach((ref, key) => {
            const refId = ref.get('id');
            if (!nodes[refId] && !nodeMap[refId]) {
              nodes[refId] = true;
              checkDependency(refId);
            }
          });

          op.getFileReferences().forEach((ref, key) => {
            const refId = ref.get('id');
            // We may not have loaded the asset, so can't clone what we don't have
            if (store.getIn(['assets', refId])) {
              assets[refId] = true;
            }
          });
        });
      });
    }

    if (cloneDependencies) forEach(checkDependency, ids);

    // Generate new ids
    forEach((id) => nodes[id] = uuid.v4(), Object.keys(nodes));
    forEach((assetId) => assets[assetId] = uuid.v4(), Object.keys(assets));

    function updateDependencies(node) {
      var resultNode = node;
      node.get('plugs').forEach((ops, plug) => {
        ops.forEach((op, opIdx) => {
          op.getReferences().forEach((ref, key) => {
            const refId = ref.get('id');
            const newRef = nodes[refId] || nodeMap[refId];
            if (newRef) resultNode = resultNode.setIn(['plugs', plug, opIdx, key], newRef);
          });

          op.getFileReferences().forEach((ref, key) => {
            const refId = ref.get('id');
            if (assets[refId]) {
              resultNode = resultNode.setIn(['plugs', plug, opIdx, key], assets[refId]);
            }
          });
        });
      });
      return resultNode;
    }


    // Remap parents, children and dependencies
    forEach((id) => {
      const node = store.getIn(['sceneGraph', id]);
      const nodeParent = node.get('parent');
      const parent = nodeParent && (nodes[nodeParent] || nodeMap[nodeParent] || nodeParent);
      const children = (node.get('children') || List())
          .map((childId) => (nodes[childId] || nodeMap[childId]))
          .filter((childId) => !!childId);

      store.dispatch({
        type: ADD_NODE,
        payload: {
          id: nodes[id],
          parent,
          node: updateDependencies(node.merge({ parent, children, id: nodes[id] })),
        }
      });
    }, Object.keys(nodes));

    var firstNodeRootId = findRoot(store, nodes[Object.keys(nodes)[0]]);

    forEach((assetId) => {
      const asset = store.getIn(['assets', assetId]);
      if (asset) {
        store.dispatch(addFile(asset.set('id', assets[assetId]).set('sceneId', firstNodeRootId).toJS()));
      }
    }, Object.keys(assets));

    return Promise.resolve(nodes);
  }
};


export function oldNodeJSON(node) {
  const jsnode = node.toJS();

  return {
    _id: jsnode.id,
    name: jsnode.name,
    type: jsnode.type,
    plugs: node.get('plugs').map((plug, type) => {
      return { type, operators: plug.map((op) => {
        return op.export();
      }).toJS() }
    }).toArray(),
    nodes: []
  };
};

/**
 * Given a list of nodes, build and return the old scene graph format.
 */
export function exportJSON(store, nodes) {
  let rootNode;
  const nodeMap = {};

  forEach((nodeId) => {
    const node = store.getIn(['sceneGraph', nodeId]);
    const parent = node.get('parent');

    const newNode = oldNodeJSON(node);
    nodeMap[newNode._id] = newNode;
    if (!rootNode) rootNode = newNode;

    if (parent && nodeMap[parent]) {
      nodeMap[parent].nodes.push(newNode);
    }
  }, nodes);

  return rootNode;
};

