/* globals THREE */
/**
 * DeviceOrientationControls - applies device orientation on object rotation
 *
 * @param {Object} object - instance of THREE.Object3D
 * @constructor
 *
 * @author richt / http://richt.me
 * @author WestLangley / http://github.com/WestLangley
 * @author jonobr1 / http://jonobr1.com
 * @author arodic / http://aleksandarrodic.com
 * @author doug / http://github.com/doug
 *
 * W3C Device Orientation control
 * (http://w3c.github.io/deviceorientation/spec-source-orientation.html)
 */
import THREE from 'three';

var screenOrientation = getOrientation();//window.orientation || 0;

function getOrientation() {
  //console.log(window.screen.orientation || window.screen.mozOrientation);
  if (__SERVER__) return 0;

  switch (window.screen.orientation || window.screen.mozOrientation) {
    case 'landscape-primary':
      return 90;
    case 'landscape-secondary':
      return -90;
    case 'portrait-secondary':
      return 180;
    case 'portrait-primary':
      return 0;
  }
  // this returns 90 if width is greater then height
  // and window orientation is undefined OR 0
  // if (!window.orientation && window.innerWidth > window.innerHeight)
  //   return 90;
  return window.orientation || 0;
}

function onScreenOrientationChangeEvent() {
  screenOrientation = getOrientation();
}
if (__BROWSER__) window.addEventListener('orientationchange', onScreenOrientationChangeEvent, false);


export default function deviceOrientationControls(object) {
  let device = {};
  device.object = object;

  device.object.rotation.reorder('YXZ');

  device.freeze = true;

  device.movementSpeed = 1.0;
  device.rollSpeed = 0.005;
  device.autoAlign = true;
  device.autoForward = false;

  device.alpha = 0;
  device.beta = 0;
  device.gamma = 0;
  device.orient = 0;

  device.alignQuaternion = new THREE.Quaternion();
  device.orientationQuaternion = new THREE.Quaternion();

  var quaternion = new THREE.Quaternion();
  var quaternionLerp = new THREE.Quaternion();

  var tempVector3 = new THREE.Vector3();
  var tempMatrix4 = new THREE.Matrix4();
  var tempEuler = new THREE.Euler(0, 0, 0, 'YXZ');
  var tempQuaternion = new THREE.Quaternion();

  var zee = new THREE.Vector3(0, 0, 1);
  var up = new THREE.Vector3(0, 1, 0);
  var v0 = new THREE.Vector3(0, 0, 0);
  var euler = new THREE.Euler();
  var q0 = new THREE.Quaternion(); // - PI/2 around the x-axis
  var q1 = new THREE.Quaternion(- Math.sqrt(0.5), 0, 0, Math.sqrt(0.5));


  device.update = (function(delta) {

    return function(event) {

      // should not need this
      //var orientation = getOrientation();
      //if (orientation !== this.screenOrientation) {
        //this.screenOrientation = orientation;
        //this.autoAlign = true;
      //}
      //console.log(deviceOrientation);
      device.alpha = event.gamma ?
        THREE.Math.degToRad(event.alpha) : 0; // Z
      device.beta = event.beta ?
        THREE.Math.degToRad(event.beta) : 0; // X'
      device.gamma = event.gamma ?
        THREE.Math.degToRad(event.gamma) : 0; // Y''
      device.orient = screenOrientation ?
        THREE.Math.degToRad(screenOrientation) : 0; // O

      if(device.freeze) return;

      // The angles alpha, beta and gamma
      // form a set of intrinsic Tait-Bryan angles of type Z-X'-Y''

      // 'ZXY' for the device, but 'YXZ' for us
      euler.set(device.beta, device.alpha, - device.gamma, 'YXZ');

      quaternion.setFromEuler(euler);
      quaternionLerp.slerp(quaternion, 0.5); // interpolate

      // orient the device
      if (device.autoAlign) device.orientationQuaternion.copy(quaternion); // interpolation breaks the auto alignment
      else device.orientationQuaternion.copy(quaternionLerp);

      // camera looks out the back of the device, not the top
      device.orientationQuaternion.multiply(q1);

      // adjust for screen orientation
      device.orientationQuaternion.multiply(q0.setFromAxisAngle(zee, - device.orient));

      device.object.quaternion.copy(device.alignQuaternion);
      device.object.quaternion.multiply(device.orientationQuaternion);

      if (device.autoForward) {

        tempVector3
          .set(0, 0, -1)
          .applyQuaternion(device.object.quaternion, 'ZXY')
          .setLength(device.movementSpeed / 50); // TODO: why 50 :S

        device.object.position.add(tempVector3);

      }

      if (device.autoAlign && device.alpha !== 0) {

        device.autoAlign = false;

        device.align();

      }

    };

  })();

  // //debug
  // window.addEventListener('click', (function(){
  //   this.align();
  // }).bind(this));
  device.connect = function(){
    device.freeze = false;
  }

  device.disconnect = function(){
    device.freeze = true;
  }

  device.align = function() {

    tempVector3
      .set(0, 0, -1)
      .applyQuaternion( tempQuaternion.copy(device.orientationQuaternion).inverse(), 'ZXY' );

    tempEuler.setFromQuaternion(
      tempQuaternion.setFromRotationMatrix(
        tempMatrix4.lookAt(tempVector3, v0, up)
     )
   );

    tempEuler.set(0, tempEuler.y, 0);
    device.alignQuaternion.setFromEuler(tempEuler);

  };
  return device;
};



