import apiBase from './apiBase';

function fetchNext(url, urls, success, failure, { contentType, method, body }) {
  function tryNext(err) {
    if (urls.length) {
      return fetchNext(urls[0], urls.slice(1), success, failure);
    } else {
      failure(err);
    }
  }

  if (contentType === 'img') {
    let img = new Image();
    img.crossOrigin = 'anonymous'; // don't send cookies, but do send Origin: header
    img.addEventListener('load', (e) => {
      success(img);
    });
    img.addEventListener('error', tryNext);
    img.src = url; //s.shift();
  } else {
    let request = new XMLHttpRequest();
    request.open(method || 'GET', url, true);
    if (body) request.setRequestHeader('Content-Type', 'application/json');

    if (contentType === 'binary') request.responseType = "arraybuffer";
    request.onload = (xhr) => {
      let response = request.response;

      if (request.status >= 200 && request.status < 400) {
        if (contentType === 'json') response = JSON.parse(response);
        success(response);
      } else {
        tryNext(request);
      }
    }
    request.onerror = tryNext;
    request.send(body);
  }
}

const callApi = apiBase(fetchNext);

export default callApi;



