import { List } from 'immutable';
import THREE from 'three';
import subdivision2 from '../../polyMesh/geometric/operator/subdivision2';
import PolyMesh from '../../polyMesh/geometric/model/PolyMesh';
import PolyMap from '../../polyMesh/geometric/model/PolyMap';
import ObjectArrayView from "../../polyMesh/generic/container/ObjectArrayView";

const UVMap = {
  schema: {
    mWidth: { type: 'Number', label: 'Width', defaultValue: 1,step: 0.1, minValue: 0.0, animatable: true },
    mHeight: { type: 'Number', label: 'Height', defaultValue: 1, step: 0.1,  minValue: 0.0, animatable: true },
    mLength: { type: 'Number', label: 'Length', defaultValue: 1, step: 0.1,  minValue: 0.0, animatable: true },

    center: { label: 'Center', type: 'Vec3', step: 0.1, defaultValue: new THREE.Vector3( 0, 0, 0 ), animatable: true },
    rotation: {label: 'Rotation', type: 'Vec3', step: 1,  minValue: -360, maxValue: 360, defaultValue: new THREE.Vector3( 0, 0, 0 ), animatable: true },
    uTile: { label: 'U Tile', type: 'Number', step: 0.1, defaultValue: 1.0, animatable: true },
    vTile: { label: 'V Tile', type: 'Number', step: 0.1, defaultValue: 1.0, animatable: true },

    //uvProjectionMode: {  label: 'Projection', type: 'Options', display: 'Radio',
    //  values: [], // FIXME UVMAP type
    //  labels: ['Planar', 'Box', 'Cylindrical', 'Spherical'], defaultValue: 0 // FIXME PROPER value },
    //xAxis: { label: 'Align Local Axis', type: 'Options', display: 'Radio',
    //  values: [AxisTypes.X, AxisTypes.Y, AxisTypes.Z, AxisTypes.AverageNormals],
    //  labels: ['X', 'Y', 'Z' , 'Average Normals'], defaultValue: AxisTypes.Y },
    //uvMapChannel: { label: 'UV Channel', type: 'Text', defaultValue: 'default' },

    autoFit: {label: 'Auto Fit', type: 'Button'},
    autoAlign: {label: 'Auto Align', type: 'Button'}, // Hidden until local manipulators fixed
    autoFitAlign: {label: 'Best Fit', type: 'Button'}, // Hidden until local manipulators fixed
    autoCenter: {label: 'Auto Center', type: 'Button'},
    resetUVs: {label: 'Reset', type: 'Button'},

    // SubObjects
    //indices: { label: "Selection", type: 'TextArea', defaultValue: "", display: 'indexStyle' },
    //componentType: { type: 'Object', hidden: true, defaultValue: 'Face' },

    // For backward compatibility
    //scale: { label: 'scale', type: 'Vec3', step: 0.1, defaultValue: {x: 0, y: 0, z: 0}, hidden: true },

    //subdivisions: { label: 'Subdivisions', type: 'Number', defaultValue: 1, minValue: 0, maxValue: 4 },
    //hardEdges: { label: 'Hard Edges', type: 'Boolean', defaultValue: false }
  },

  update: function(operator, primitive) {
    const mesh = primitive.get('mesh');
    if(!mesh) return primitive;

    const options = {};

    const mLength = operator.get('mLength');
    const mWidth = operator.get('mWidth');
    const mHeight = operator.get('mHeight');

    options.uTile = operator.get('uTile');
    options.vTile = operator.get('vTile');
    options.center = operator.get('center');
    options.rotation = operator.get('rotation');
    options.mapChannel = operator.get('uvMapChannel');
    options.scale = new THREE.Vector3(mWidth, mHeight, mLength);

    const newPolymesh = applyUVMap(mesh, options);

    return primitive.set('mesh', newPolymesh);;
  }
}
//*//WIP
function applyUVMap( polyMesh, uvMapOptions ) {

  uvMapOptions = uvMapOptions || {};    // because all values can be defaulted, uvMapOptions should be allowed to be null/undefined too!

  const rotation = uvMapOptions.rotation.multiplyScalar( 180 / Math.PI ) || new THREE.Vector3();
  const scale = uvMapOptions.scale || new THREE.Vector3( 1, 1, 1 );
  const center = uvMapOptions.center || new THREE.Vector3();
  const mapChannel = ( uvMapOptions.mapChannel !== undefined ) ? uvMapOptions.mapChannel : 'default';
  const uTile = (uvMapOptions.uTile !== undefined) ? uvMapOptions.uTile : 1.0;
  const vTile = (uvMapOptions.vTile !== undefined) ? uvMapOptions.vTile : 1.0;

  // Inverse matrix should be used, so uv map follows the gimzo
  const matrixRotation = new THREE.Matrix4().makeRotationFromEuler( new THREE.Euler().setFromVector3( rotation, 'ZYX' ) );
  const matrixRotationInv = new THREE.Matrix4().getInverse( matrixRotation );

  const faceRangeOffsets = polyMesh.faceRangeOffsets;
  const faceValueIndices = polyMesh.positions.faceValueIndices;
  const values = polyMesh.positions.values;

  const uvFaceValueIndices = new Uint32Array(faceValueIndices.length);
  const uvValues = new ObjectArrayView(THREE.Vector2, faceValueIndices.length);

  const vertex1 = new THREE.Vector3();
  const vertex2 = new THREE.Vector3();
  const faceNormal = new THREE.Vector3();
  const newUV = new THREE.Vector2();

  //for every face
  let faceBegin = 0;
  let faceEnd = 0;
  let uvValueCounter = 0;
  for(let i = 1; i<faceRangeOffsets.length; i++){
    faceEnd = faceRangeOffsets[i];

    //compute the face normal
    faceNormal.multiplyScalar(0);
    for ( let j = faceBegin; j<faceEnd; j++ ) {

      let k = j + 1;
      if(k >= faceEnd){
        k = faceBegin;
      }
      values.getAt(faceValueIndices[j], vertex1);
      values.getAt(faceValueIndices[k], vertex2);

      faceNormal.x += (vertex1.y - vertex2.y) * (vertex1.z + vertex2.z);
      faceNormal.y += (vertex1.z - vertex2.z) * (vertex1.x + vertex2.x);
      faceNormal.z += (vertex1.x - vertex2.x) * (vertex1.y + vertex2.y);
    }
    faceNormal.normalize();

    //for every index on that face
    for (let j = faceBegin; j<faceEnd; j++) {

      values.getAt(faceValueIndices[j], vertex1);

      vertex1.sub(center);    // Translate to center
      vertex1.applyMatrix4( matrixRotationInv );   // Apply Inverse rotation to the vertex (to follow gizmo)

      boxUVMap( vertex1, faceNormal, scale, newUV );

      // Apply tiling
      if ( uTile !== 1.0 ) {
        newUV.x *= uTile;
      }

      if ( vTile !== 1.0 ) {
        newUV.y *= vTile;
      }

      // Avoid disconnected uv faces
      //TODO check for duplicate uv's on that vertex and don't store it twice;

      uvFaceValueIndices[j] = uvValueCounter;
      uvValues.setAt(uvValueCounter++, newUV );
    }

    faceBegin = faceEnd;
  }

  const resultMesh = new PolyMesh(polyMesh);

  const newUVMap = new PolyMap( {
   faceRangeOffsets: faceRangeOffsets,
   faceValueIndices: uvFaceValueIndices,
   values: uvValues
  });

  resultMesh.uvMaps.set( mapChannel, newUVMap );

  return resultMesh;
};


function boxUVMap( vertex, normal, scale, uv ) {

  let s = 0;
  let t = 0;

  const x = vertex.x;
  const y = vertex.y;
  const z = vertex.z;
  // Face normal
  const nx = Math.abs( normal.x );
  const ny = Math.abs( normal.y );
  const nz = Math.abs( normal.z );

  if (nx >= ny && nx >= nz) {
    s = -z / scale.z + 0.5;
    t = y / scale.y + 0.5;
  }
  if (ny >= nx && ny >= nz) {
    s = x / scale.x + 0.5;
    t = -z / scale.z + 0.5;
  }
  if (nz >= nx && nz >= ny) {
    s = x / scale.x + 0.5;
    t = y / scale.y + 0.5;
  }

  // Otherside of box is mirror of the other side
  // However, only making them negative is not a good solution for tiling, so we add them to 1 to be flipped in positive side
  if ( normal.x < 0 ) {
    s = -s + 1;
  }
  if ( normal.y < 0 ) {
    s = -s + 1;
  }
  if ( normal.z < 0 ) {
    s = -s + 1;
  }

  uv.set( s, t );
  return uv;
};
//*/

export default UVMap;



