import THREE from 'three';
import ObjectArrayView from '../../generic/container/ObjectArrayView';
import ObjectBuffer from '../../generic/container/ObjectBuffer';
import PolyMesh from '../model/PolyMesh';
import PolyMap from '../model/PolyMap';

export default function removeDuplicateValues( polyMesh, withLogging ) {

  //removes indentical values in the values array and remaps the indices
  //also has the side effect of removing values not used by a face

  const l = withLogging || false;

  const faceValueIndices = new Uint32Array(polyMesh.positions.faceValueIndices);
  const values = polyMesh.positions.values;

  const newValues = new ObjectBuffer(THREE.Vector3);

  const valueMappings = new Map();
  let newValuesLength = 0;

  let key = 0;
  let newIndex = 0;
  const value = new THREE.Vector3;

  for( let ix = 0; ix < faceValueIndices.length; ix++){

    values.getAt(faceValueIndices[ix], value);
    //hash function rounds to 3 decimal places and creates a 'unique' value for the vertex
    //may fail if a vertex has a coordinate over 1,000,000 although even if that happens collision is unlikly
    key = 1000000000000*Math.round(value.x*1000) + 100000*Math.round(value.y*1000) + Math.round(value.z*1000);

    if( !valueMappings.has(key) ){
      valueMappings.set(key, newValuesLength);//save location of value in map
      newIndex = newValuesLength;
      newValues.push(value);//push value to the newValues array
      newValuesLength++;
    }
    else {
      newIndex = valueMappings.get(key);
    }

    faceValueIndices[ix] = newIndex;//update indices array with new value

  }

  if(l) console.log('' + values.length + ' values reduced to ' + newValuesLength + ', duplicates removed: ' + (values.length-newValuesLength));

  if(values.length === newValuesLength) return polyMesh;

  const newValuesArray = new ObjectArrayView(THREE.Vector3, newValuesLength);
  newValuesArray.fromArray(newValues.toArray());

  const newPositions = new PolyMap( {
   faceRangeOffsets: polyMesh.faceRangeOffsets,
   faceValueIndices: faceValueIndices,
   values: newValuesArray
  });


  const resultMesh = new PolyMesh( polyMesh );
  resultMesh.positions = newPositions;

  return resultMesh;
};



