import { List } from 'immutable';
import { FileReference, NodeReference, PlugReference, Vec2, Vec3, Color} from './primitives';

const schemaTypes = {
  'Boolean': function(schema) {
    let defaultValue = schema.defaultValue || false;

    return {
      defaultValue,

      set: (val) => {
        return typeof val == null ? defaultValue : !!val;
      }
    }
  },

  'Label': function(schema) {
    return {set: (val) => val};
  },

  'Number': function(schema) {
    let min = schema.minValue;
    let max = schema.maxValue;
    let defaultValue = schema.defaultValue || 0;

    let clamp;

    if (min !== undefined && max !== undefined) clamp = (v) => Math.min(Math.max(min, v), max);
    else if (min !== undefined) clamp = (v) => Math.max(v, min);
    else if (max !== undefined) clamp = (v) => Math.min(v, max);
    else clamp = (v) => v;

    return {
      defaultValue,

      set: (val) => {
        let num = Number(val);
        if (isNaN(num)) return defaultValue;
        return Math.round(clamp(num) * 10000) / 10000;
      }
    }
  },

  'Integer': function(schema) {
    let min = schema.minValue;
    let max = schema.maxValue;
    let defaultValue = schema.defaultValue || 0;

    let clamp;

    if (min !== undefined && max !== undefined) clamp = (v) => Math.min(Math.max(min, v), max);
    else if (min !== undefined) clamp = (v) => Math.max(v, min);
    else if (max !== undefined) clamp = (v) => Math.min(v, max);
    else clamp = (v) => v;

    return {
      defaultValue,

      set: (val) => {
        let num = Number(val);
        if (isNaN(num)) return defaultValue;
        return parseInt(clamp(num));
      }
    }
  },

  'Plug': function(schema) {
    if (!schema.plug) throw new Error("plug property expects a `plug` property");

    return {
      isReference: true,

      set: (id) => {
        return id ? PlugReference({id, plug: schema.plug}) : null;
      },
    }
  },

  'Options': function(schema) {
    let defaultValue = schema.defaultValue;
    return {
      set: (val) => {
        return typeof val == null ? defaultValue : val;
      }
    }
  },

  'Image': function(schema) {
    return {
      isFileReference: true,

      set: (id) => {
        return id ? FileReference({id}) : null;
      },
    }
  },

  'Binary': function(schema) {
    return {
      isFileReference: true,

      set: (id) => {
        return id ? FileReference({id}) : null;
      },
    }
  },

  'File': function(schema) {
    return {
      isFileReference: true,

      set: (id) => {
        return id ? FileReference({id}) : null;
      },
    }
  },

  'Object': function(schema) {
    let defaultValue = schema.defaultValue || {};
    return {
      set: (obj) => {
        return obj || defaultValue;
      }
    }
  },

  'Text': function(schema) {
    let defaultValue = schema.defaultValue || '';

    return {
      defaultValue,

      set: (val) => {
        return typeof val === 'string' ? val : ''+val;
      }
    }
  },

  'String': function(schema) {
    let defaultValue = schema.defaultValue;

    return {
      defaultValue,

      set: (str) => str || defaultValue
    }
  },

  'Node': function(schema) {
    return {
      isReference: true,

      set: (id) => {
        return id ? NodeReference({id}) : null;
      },
    }
  },

  'NodeList': function(schema) {
    return {
      isReference: true,

      set: (ids) => {
        if (Array.isArray(ids)) return List(ids.map((id) => {
          return typeof id === 'string' ? NodeReference({id}) : id;
        }));
        if (ids.map) return ids.map((id) => {
          return typeof id === 'string' ? NodeReference({id}) : id;
        });
        return List();
      },
    }
  },
    // return {value: val, references: {[val]: true}};

  'Vec2': function(schema) {
    let defaultValue = Vec2(schema.defaultValue);

    return {
      defaultValue,

      set: (val) => {
        return val ? Vec2(val) : defaultValue;
      }
    }
  },

  'Color': function(schema) {
    let defaultValue = Color(schema.defaultValue);

    return {
      defaultValue,

      set: (val) => {
        if (!val) return defaultValue;
        return Color(val);
      }
    }
  },

  'Vec3': function(schema) {
    let defaultValue = Vec3(schema.defaultValue);

    return {
      defaultValue,

      set: (val) => {
        return val ? Vec3(val) : defaultValue;
      }
    }
  }
}

export default schemaTypes;



