import { readwrite, convertToPolyMesh, convertToCNSPolyMesh, convertToBingeom } from '../../translators/bingeom';
import { getOrFetchBinary, getHash } from '../../modules/assets';
import { isSceneLoaded } from '../../modules/sceneIO';

const Mesh = {
  schema: {
    geometry: {type: 'File', label: 'PolyMesh Geometry File'}
  },

  update: function(operator, primitive, { node, store, properties }) {
    const visible = properties.get('visible');
    const geom = operator.get('geometry'); // Request geometry so the asset gets referenced
    const fetch = visible || (properties.get('prefetch') && isSceneLoaded(store))
    const ref = fetch && operator.get('geometry');
    const bingeom = ref && store.hasApi() && getOrFetchBinary(ref.get('id'))(store);
    const mesh = bingeom && convertToCNSPolyMesh(bingeom, ref.get('id'));
    //console.log('Mesh update', 'visible:', visible, 'prefetch:', properties.get('prefetch'), 'fetch:', fetch, 'bingeom:', !!bingeom, ref && ref.get('id'), node.get('name'), mesh);
    return mesh ? primitive.set('mesh', mesh) : primitive;
  },
}

export default Mesh;




