import { convertToCNSPolyMesh } from '../translators/bingeom';
import { getOrFetchBinary, getHash } from '../modules/assets';
import { areAssetsLoaded } from '../modules/sceneIO';

import MeshSmooth from './PolyMesh/MeshSmooth';

const StaticMesh = {
  schema: {
    reference: {type: 'Binary', label: 'Binary Geometry File'}
  },
  update: (operator, primitive) => {
    return [['bingeom', [operator.get('reference').get('id')]]]
  }
}

const ProxyMesh = {
  schema: {
    binary: {type: 'Binary', label: 'Binary Geometry File'},
    reducedBinary: {type: 'Binary', label: 'Reduced Binary Geometry File'},
    operatorDisplay: { type: "Number", defaultValue: 1, label: 'Operator Display Type' },
    nbLevel: { type: "Number", defaultValue: 1}, // 1 -> only binary, 2 -> level0 and binary, 3 -> level0, level1, binary, ....
    level0: { type: 'Binary', label: 'Binary Geometry File'},
    level1: { type: 'Binary', label: 'Binary Geometry File'},
    level2: { type: 'Binary', label: 'Binary Geometry File'},
    level3: { type: 'Binary', label: 'Binary Geometry File'},
    level4: { type: 'Binary', label: 'Binary Geometry File'},
  },

  update: function(operator, primitive, { node, store, properties }) {
    const visible = properties.get('visible');
    const binary = operator.get('binary');
    const fetch = visible || (properties.get('prefetch') && areAssetsLoaded(store))
    const bingeom = fetch && binary && store.hasApi() && getOrFetchBinary(binary.get('id'))(store);
    const mesh = bingeom && convertToCNSPolyMesh(bingeom, binary.get('id'));
    //console.log('Mesh update', 'visible:', visible, 'prefetch:', properties.get('prefetch'), 'fetch:', fetch, 'bingeom:', !!bingeom, ref && ref.get('id'), node.get('name'), mesh);
    return mesh ? primitive.set('mesh', mesh) : primitive;
  },
}

export default { ProxyMesh, MeshSmooth };



