import ObjectsByName from '../../generic/container/ObjectsByName';
import PolyMaps from './PolyMaps';
import PolyMap from './PolyMap';

export default function PolyMesh( from ) {

  if(!from){
    this.faceRangeOffsets = new Uint32Array(1);
    this.positions = new PolyMap();
  } else {

    this.faceRangeOffsets = from.faceRangeOffsets;  //Uint32Array
    this.materialIds = from.materialIds;    //PolyMap

    this.positions = from.positions;        //PolyMap

    this.normalMap = from.normalMap;        //PolyMap
    this.tangentMap = from.tangentMap;      //PolyMap
    this.colorMaps = ObjectsByName.shallowClone( from.colorMaps );  //ObjectsByName contains PolyMap
    this.uvMaps = ObjectsByName.shallowClone( from.uvMaps );        //ObjectsByName contains PolyMap

    this.skinning = from.skinning;          //Skinning
  }

};

PolyMesh.prototype = {

  constructor: PolyMesh,

  getNumFaces: function() {

    return this.faceRangeOffsets.length - 1;

  },

  getNumFaceVertices: function() {

    return this.positions.faceValueIndices.length;

  },

  getNumVertices: function() {

    return this.positions.values.length;

  },

  getMapById: function( mapId ) {

    return PolyMaps.resolveMap( this, mapId ) || null;

  }

};

PolyMesh.fromData = function(
    faceRangeOffsets, positions, optionalIndices, optionalMaterialIds ) {

  return new PolyMesh( {

    faceRangeOffsets: faceRangeOffsets,

    positions: positions instanceof PolyMap ? positions :
        PolyMap.fromData(
          faceRangeOffsets, optionalIndices || null, positions ),

    materialIds: optionalMaterialIds || null, // Uint32Array

    normalMap: null,  // optional PolyMap< THREE.Vector3 >
    tangentMap: null, // optional PolyMap< THREE.Vector3 >

    uvMaps: null,   // optional ObjectsByName< PolyMap<THREE.Vector2> >
    colorMaps: null,  // optional ObjectsByName< PolyMap<THREE.Color> >

    skinning: null

  } );

};



